/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState, useEffect } from "react";

import { Power } from "react-bootstrap-icons";
import Spinner from "react-bootstrap/Spinner";

import Modal from "../../../components/Modal";
import { logOut, checkFirstLogin, initialTheme } from "../../../utils/lib";
import { defaultMenuList, defaultMainMenuList } from "./DefaultMenu";
import OnboardingSteps from "../../../components/Joyride/OnboardingSteps"; // <------- ADD THIS LINE ...

import MenuList from "./MenuList";

const Home = ({ dateLogin, setActiveMenu, initialValue, setInitialValue, isPage }) => {
  const { HOMEURL, WELCOMEMODAL } = initialTheme;
  const user = JSON.parse(localStorage.getItem("user"));
  const [credit, setCredit] = useState(user.lastCredit ? user.lastCredit : '0.00');
  const [show, setShow] = useState(false);
  const [modalType, setModalType] = useState("");
  const [isLoading, setIsLoading] = useState(true);
  const [isTour, setIsTour] = useState(
    initialValue.tutorial === "N" ? false : user.welcomemodal
  );
  const [isModalWelcome, setIsModalWelcome] = useState(
    WELCOMEMODAL && !isTour ? user.welcomemodal : false
  );
  const handleClose = () => {
    if (user.welcomemodal) {
      const userUpdateModal = { ...user, welcomemodal: false };
      setIsModalWelcome(userUpdateModal.welcomemodal);
      localStorage.setItem("user", JSON.stringify(userUpdateModal));
    }
    setShow(false);
  };
  const handleShow = (modalType) => {
    setShow(true);
    setModalType(modalType);
  };

  useEffect(() => {
    document.body.style.overflowX = "hidden";
    checkFirstLogin(dateLogin);
  }, []);
  // [initialTheme, isModalWelcome]);

  return (
    <div id="card-game" className="text-white fade-in">
      <div
        className="container"
        style={{
          opacity: isLoading ? "unset" : 0.4,
          pointerEvents: isLoading ? "unset" : "none",
        }}
      >
        {initialValue.tutorial && (
          <OnboardingSteps
            initialTheme={initialTheme}
            memberTutorial={initialValue.tutorial}
            memberusername={user.memberusername}
            setIsModalWelcome={setIsModalWelcome}
            isTour={isTour}
            setIsTour={setIsTour}
          />
        )}
        <div className="row pt-4">
          <div className="col-8 txt_head_set">
            <div
              id="tour-username"
              className="step2"
              style={{ width: "150px" }}
            >
              <h4>ชื่อผู้ใช้</h4>
              <p>{user.memberusername}</p>
            </div>
          </div>
          <div className="col-4 txt_head_set text-right">
            <a onClick={() => logOut(HOMEURL)} style={{ cursor: "pointer" }}>
              <h4 className="txt_logout">
                <Power size={20} />
              </h4>
              <p className="txt_logout">ออกจากระบบ</p>
            </a>
          </div>
        </div>
        <MenuList
          dateLogin={dateLogin}
          mainMenuList={defaultMainMenuList}
          menuList={defaultMenuList}
          handleShow={handleShow}
          setIsLoading={setIsLoading}
          setActiveMenu={setActiveMenu}
          setCredit={setCredit}
          credit={credit}
          initialTheme={initialTheme}
          isPage={isPage}
        />
      </div>
      <Modal
        show={isModalWelcome ? true : show}
        setShow={setShow}
        handleClose={handleClose}
        type={isModalWelcome ? "annou" : modalType}
        initialValue={initialValue}
        setInitialValue={setInitialValue}
        credit={credit}
        setCredit={setCredit}
        initialTheme={initialTheme}
        dateLogin={dateLogin}
      />
      <Spinner
        animation="border"
        variant="warning"
        style={{
          position: "absolute",
          display: isLoading ? "none" : "unset",
          color: "primary.main",
        }}
      />
    </div>
  );
};
export default Home;
