/* eslint-disable react/jsx-no-target-blank */
import React, { useState, useEffect } from "react";
import axios from "axios";
import { Base64 } from "js-base64";

import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";
import * as Icon from "react-bootstrap-icons";
import Carousel from "react-bootstrap/Carousel";
import Spinner from "react-bootstrap/Spinner";

import { onClickCopy, appConfig, checkLastLogin } from "../../../utils/lib";
import { getRecommend } from "../../../utils/apiPath";

export default function Recommend({
  dateLogin,
  Affiliate,
  setAffiliateStatus,
  setCashbackStatus,
}) {
  const user = JSON.parse(localStorage.getItem("user"));
  const [recommendValue, setRecommendValue] = useState("");
  const [isLoading, setIsLoading] = useState(true);
  const { afclick, afregister, income, canwithdraw } = recommendValue;

  let afUrlShare =
    (Affiliate ? Affiliate : window.location.origin + "/register") +
    `?uplineid=${Base64.encode(user.memberid)}`;

  const setUpRecommend = async () => {
    const body = { memberid: user.memberid };
    await axios({
      url: `${appConfig.REACT_APP_APIURL}${getRecommend}`,
      method: "POST",
      timeout: 1000 * 7,
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
      },
      data: body,
    })
      .then((res) => {
        setIsLoading(false);
        let result = res.data.result;
        result = {
          ...result,
          canwithdraw: result.canwithdraw.toFixed(2),
          income: result.income.toFixed(2),
          winlosscom: result.winlosscom.toFixed(2),
        };
        setRecommendValue(result);
        setAffiliateStatus(result.affiliateInfo.activestatus);
        setCashbackStatus(result.cashbackInfo[5].configvalue);
      })
      .catch((err) => {
        setIsLoading(false);
        console.log("error", err);
      });
  };

  useEffect(() => {
    setUpRecommend();
    checkLastLogin();
  }, []);

  return (
    <>
      {!isLoading && (
        <>
          <div className="form-group text-center mb-0 px-1 fade-in ">
            <div className="row mt-2 mb-0 mb-4 justify-content-md-center">
              <div className="col-sm-12 col-lg-12 col-12">
                <h3 className="text-center mb-3" style={{ fontWeight: "500" }}>
                  ลิงก์รับทรัพย์
                </h3>
                <div className="form-group">
                  <InputGroup className="mb-3">
                    <FormControl
                      className="frm-resetpass"
                      id="memberphone"
                      name="memberphone"
                      placeholder="เบอร์โทรศัพท์"
                      aria-label="memberphone"
                      aria-describedby="basic-addon1"
                      value={afUrlShare}
                    />
                    <InputGroup.Text
                      className="btn-copy-link btn_bg "
                      id="basic-addon1"
                      onClick={(e) => onClickCopy(afUrlShare, "link")}
                    >
                      <Icon.Files />
                      {`${" คัดลอก"}`}
                    </InputGroup.Text>
                  </InputGroup>
                </div>
                <div className="form-group">
                  <div className="row mt-3 mb-0 justify-content-md-center">
                    <div className="col-6">
                      <a
                        href={`${"https://social-plugins.line.me/lineit/share?url=" +
                          afUrlShare
                          }`}
                        target="_blank"
                        className="btn btn-line btn-sm btn-block"
                      >
                        <img
                          src={
                            window.location.origin +
                            "/assets/images/icon-line.png"
                          }
                          style={{
                            maxWidth: "15px",
                            display: "inline-block",
                            marginRight: "3px",
                          }}
                          alt=""
                        />
                        {`${" แชร์"}`}
                      </a>
                    </div>
                    <div className="col-6">
                      <a
                        href={`${"https://www.facebook.com/sharer/sharer.php?u=" +
                          afUrlShare
                          }`}
                        target="_blank"
                        className="btn btn-facebook btn-sm btn-block"
                      >
                        <Icon.Facebook />
                        {`${" แชร์"}`}
                      </a>
                    </div>
                  </div>
                </div>
                <p className="text-white mt-4 mb-0">
                  {`${" ส่ง Link ได้ทุกช่องทางรับทรพย์กับเรา"}`}
                </p>
              </div>
            </div>
          </div>
          <div className="text-title text-center">รายได้ผู้แนะนำ</div>
          <div className="row no-gutters">
            <div className="col-6 d-none">
              <div className="card-report text-center">
                <p className="mb-1">คลิกลิ้ง</p>
                <p className="mb-0">{afclick}</p>
              </div>
            </div>
            <div className="col-12">
              <div className="card-report text-center">
                <p className="mb-1">สมัคร</p>
                <p className="mb-0">{afregister}</p>
              </div>
            </div>

            <div className="col-6">
              <div className="card-report text-center">
                <p className="mb-1">รายได้ทั้งหมด</p>
                <p className="mb-0">{income}</p>
              </div>
            </div>
            <div className="col-6">
              <div className="card-report text-center">
                <p className="mb-1">รายได้ที่ถอนได้</p>
                <p className="mb-0">{canwithdraw}</p>
              </div>
            </div>
          </div>
          <div className="row">
            <div className="col-12">
              <Carousel controls={false}>
                <Carousel.Item interval={1000}>
                  <img
                    className="d-block w-100"
                    src={
                      window.location.origin +
                      "/assets/images/%E0%B8%A3%E0%B8%B1%E0%B8%9A%E0%B8%97%E0%B8%A3%E0%B8%B1%E0%B8%9E%E0%B8%A2%E0%B9%8C1.jpg"
                    }
                    alt="First slide"
                  />
                </Carousel.Item>
                <Carousel.Item interval={500}>
                  <img
                    className="d-block w-100"
                    src={
                      window.location.origin +
                      "/assets/images/%E0%B8%A3%E0%B8%B1%E0%B8%9A%E0%B8%97%E0%B8%A3%E0%B8%B1%E0%B8%9E%E0%B8%A2%E0%B9%8C2.jpg"
                    }
                    alt="Second slide"
                  />
                </Carousel.Item>
                <Carousel.Item>
                  <img
                    className="d-block w-100"
                    src={
                      window.location.origin +
                      "/assets/images/%E0%B8%A3%E0%B8%B1%E0%B8%9A%E0%B8%97%E0%B8%A3%E0%B8%B1%E0%B8%9E%E0%B8%A2%E0%B9%8C3.jpg"
                    }
                    alt="Third slide"
                  />
                </Carousel.Item>
              </Carousel>
            </div>
          </div>
        </>
      )}
      <Spinner
        animation="border"
        variant="warning"
        style={{
          position: "absolute",
          display: isLoading ? "unset" : "none",
          color: "primary.main",
        }}
      />
    </>
  );
}
