/* eslint-disable no-script-url */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect, useState } from "react";
import moment from "moment";

import { ArrowRepeat } from "react-bootstrap-icons";
import {
  fecthGetBalance,
  redirActivity,
  checkLastLogin,
  appConfig
} from "../../../utils/lib";

import { v4 as uuidv4 } from "uuid";

export const MenuList = ({
  dateLogin,
  mainMenuList,
  menuList,
  handleShow,
  setIsLoading,
  setActiveMenu,
  setCredit,
  credit,
  initialTheme,
  isPage,
}) => {
  var timeNow = new Date();
  const [isTime, setIsTime] = useState(moment(timeNow).format("HH:mm:ss"));
  const [isTimeDelay, setIsTimeDelay] = useState(moment(timeNow).format("HH:mm:ss"));

  const user = JSON.parse(localStorage.getItem("user"));
  const { HOMEURL, THEME, AISTATUS, LINEURL } =
    initialTheme || "";

  useEffect(() => {
    // checkLastLogin(true);
    if (isPage) {
      setIsLoading(false);
      setTimeout(() => {
        recheckCredit();
        setIsLoading(true);
      }, user.welcomemodal ? 0 : 2000);
    } 
  }, []);

  const recheckCredit = async (e) => {
    if (e) {
      setIsLoading(false);
      checkLastLogin(true);
    }

    if (isTimeDelay <= moment(new Date()).format("HH:mm:ss")) {
      var getCreadit = await fecthGetBalance();
      const userLastCredit = { ...user, lastCredit: getCreadit.value };
      localStorage.setItem("user", JSON.stringify(userLastCredit));
      if (getCreadit.status) {
        setCredit(getCreadit.value);
      }
      setIsLoading(getCreadit.status);
      setIsTimeDelay(moment(new Date()).add(5, 'seconds').format("HH:mm:ss"));
    } else {
      setIsTime(moment(timeNow).format("HH:mm:ss"));
    }
    setTimeout(() => { setIsLoading(true); }, 350);

  };

  const onClickActivity = async () => {
    await checkLastLogin();
    await redirActivity();
  };

  return (
    <div key={uuidv4()} className="row section_menu_list pb-8">
      <div className="col-12 mb-4">
        <div
          onClick={(e) => recheckCredit(e)}
          className="section_deposit_total step3"
          style={{ cursor: "pointer" }}
        >
          <img
            src={`${window.location.origin}/images/wallet.png?${appConfig.REACT_APP_IMGVER}`}
            style={{ maxWidth: "50px" }}
            alt=""
          />
          <h5>กระเป๋าเงิน</h5>
          <h2>
            <span id="customer-balance">
              {credit}
            </span>{" "}
            บาท
          </h2>
        </div>
        <p className="text-center mt-1 txt_reload_credits">
          <ArrowRepeat /> ข้อมูล ณ เวลา{" "}
          <span className="credit_lastupdate">{isTime}</span>
        </p>
      </div>
      {mainMenuList.length > 0 &&
        mainMenuList.map((item, index) => {
          return (
            <>
              {item.position ? (
                <div key={uuidv4()} className="col-4 section_menu mb-2">
                  <div className=" btn-gold animation-gold step6">
                    <a id="" href={THEME === "1" ? HOMEURL : item.path}>
                      <div
                        className="section_menu_set tour-btnplay"
                        style={{ minWidth: "85px", minHeight: "85px" }}
                      >
                        <img
                          src={item.image}
                          className="img_icon_set logo_BETFLIX_icon_set"
                          style={{
                            maxWidth: "50px",
                            top: "18px",
                            maxHeight: "46px",
                          }}
                          alt={item.name}
                        />
                      </div>
                    </a>
                  </div>
                  <h5
                    className="text-center"
                    style={{ position: "relative", top: "-20px" }}
                  >
                    เข้าเล่น
                  </h5>
                </div>
              ) : (
                <div
                  key={uuidv4()}
                  className="col-4 section_menu mb-2"
                  style={item.style[0]}
                >
                  <div
                    className={`section_menu_set tour-deposit ${item.modalType === "withdraw" ? "step5" : "step4"
                      }`}
                    style={item.style[1]}
                  >
                    <a
                      onClick={() => handleShow(item.modalType)}
                      className="menu_txt startCheck"
                    >
                      <img
                        src={item.image}
                        className="img_icon_set"
                        style={item.style[3]}
                        alt={item.name}
                      />
                    </a>
                  </div>
                  <h5 style={item.style[2]}>{item.name}</h5>
                </div>
              )}
            </>
          );
        })}
      <div className="col-3 section_menu mb-2">
        <a onClick={() => setActiveMenu("promotion")} className="menu_txt">
          <div className="section_menu_set">
            <img
              src={`${window.location.origin}/images/ic_tr4.png?${appConfig.REACT_APP_IMGVER}`}
              className="img_icon_set"
              style={{ maxWidth: "30px" }}
              alt="promotion"
            />
          </div>
          <h5 className="text-center">โปรโมชั่น</h5>
        </a>
      </div>
      <div className="col-3 section_menu mb-2">
        <a onClick={() => onClickActivity()} className="menu_txt">
          <div className="section_menu_set">
            <img
              src={`${window.location.origin}/images/ic_tr5.png?${appConfig.REACT_APP_IMGVER}`}
              className="img_icon_set"
              style={{ maxWidth: "40px" }}
              alt="activity"
            />
          </div>
          <h5 className="text-center">กิจกรรม</h5>
        </a>
      </div>
      <div className="col-3 section_menu mb-2">
        <a onClick={() => setActiveMenu("history")} className="menu_txt">
          <div className="section_menu_set">
            <img
              src={`${window.location.origin}/images/ic_tr9.png?${appConfig.REACT_APP_IMGVER}`}
              className="img_icon_set"
              style={{ maxWidth: "40px" }}
              alt="history"
            />
          </div>
          <h5 className="text-center">ประวัติ</h5>
        </a>
      </div>
      <div className="col-3 section_menu mb-2">
        <a onClick={() => setActiveMenu("recommend")} className="menu_txt">
          <div className="section_menu_set">
            <img
              src={`${window.location.origin}/images/ic_tr3.png?${appConfig.REACT_APP_IMGVER}`}
              className="img_icon_set"
              style={{ maxWidth: "40px" }}
              alt="recommend"
            />
          </div>
          <h5 className="text-center">รับทรัพย์</h5>
        </a>
      </div>
      <div className="col-3 section_menu mb-2">
        <a onClick={() => setActiveMenu("profile")} className="menu_txt">
          <div className="section_menu_set">
            <img
              src={`${window.location.origin}/images/ic_tr8.png?${appConfig.REACT_APP_IMGVER}`}
              className="img_icon_set"
              style={{ maxWidth: "30px" }}
              alt="profile"
            />
          </div>
          <h5 className="text-center">บัญชีผู้ใช้</h5>
        </a>
      </div>
      <div className="col-3 section_menu mb-2">
        <a onClick={() => handleShow("annou")} className="menu_txt">
          <div className="section_menu_set">
            <img
              src={`${window.location.origin}/images/ic_tr6.png?${appConfig.REACT_APP_IMGVER}`}
              className="img_icon_set"
              style={{ maxWidth: "36px" }}
              alt="annou"
            />
          </div>
          <h5 className="text-center">ประกาศ</h5>
        </a>
      </div>
      <div className="col-3 section_menu mb-2">
        <a
          onClick={() => window.open(LINEURL ? LINEURL : "", "_blank")}
          className="menu_txt"
        >
          <div className="section_menu_set">
            <img
              src={`${window.location.origin}/images/ic_tr10.png?${appConfig.REACT_APP_IMGVER}`}
              className="img_icon_set"
              style={{ maxWidth: "35px" }}
              alt="line"
            />
          </div>
          <h5 className="text-center">ติดต่อ</h5>
        </a>
      </div>
      <div className="col-3 section_menu mb-2">
        {AISTATUS || AISTATUS !== "N" ? (
          <a onClick={() => handleShow("ai")} className="menu_txt">
            <div className="section_menu_set">
              <img
                src={`${window.location.origin}/assets/images/icon_ai.png?${appConfig.REACT_APP_IMGVER}`}
                className="img_icon_set"
                style={{ maxWidth: "36px" }}
                alt="website"
              />
            </div>
            <h5 className="text-center">สูตร AI</h5>
          </a>
        ) : (
          <a onClick={() => window.open("/", "_blank")} className="menu_txt">
            <div className="section_menu_set">
              <img
                src={`${window.location.origin}/images/ic_tr11.png?${appConfig.REACT_APP_IMGVER}`}
                className="img_icon_set"
                style={{ maxWidth: "36px" }}
                alt="website"
              />
            </div>
            <h5 className="text-center">เว็บไซต์</h5>
          </a>
        )}
      </div>
    </div>
  );
};
export default MenuList;
