/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect, useState } from "react";
import axios from "axios";

// import swal from "sweetalert";
import { useParams } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";
import Spinner from "react-bootstrap/Spinner";
import * as Icon from "react-bootstrap-icons";
import swal from "sweetalert2";
import Modal from "../../components/Modal";


import { setUpLastLogin } from "../../utils/lib";
import { loginAuth, getTheme } from "../../utils/apiPath";

export const alertText = [
  "กรุณากรอก เบอร์โทรศัพท์ / รหัสผ่าน",
  "เบอร์โทรศัพท์ / รหัสผ่าน ไม่ถูกต้องค่ะ",
];

const Login = () => {
  const appConfig = window.globalConfig || {
    REACT_APP_APIURL: process.env.REACT_APP_APIURL,
    REACT_APP_KEYBRAVO: process.env.REACT_APP_KEYBRAVO,
    REACT_APP_IMGURL: process.env.REACT_APP_IMGURL,
  };

  const [username, setUserName] = useState("");
  const [password, setPassword] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [show, setShow] = useState(false);
  const [showPassword, setShowPassword] = useState(true);
  const handleShow = () => setShow(true);
  const handleClose = () => setShow(false);
  const handleShowPassword = () => setShowPassword(!showPassword);

  const [getLogo, setLogo] = useState();
  const [getBgColor, setBgColor] = useState();
  const [getBgColor2, setBgColor2] = useState();
  const [getBgMainColor, setBgMainColor] = useState();
  const [theme, setTheme] = useState("");
  const [homeUrl, setHomeUrl] = useState("");
  var bgColor0, bgColor1, bgColor2, bgColor, _webname;

  useEffect(() => {
    setUpTheme();
    if(isLoading){
        setUpLastLogin(theme);
    }
  }, [isLoading]);

  const renderTheme = () => {
    let _storeTheme;
    _webname = localStorage.getItem("webname");
    if (_webname) {
      _storeTheme = localStorage.getItem(`initialTheme${_webname}`);
      if (_storeTheme) {
        _storeTheme = JSON.parse(_storeTheme);
        bgColor0 = _storeTheme.ROOTSTYLE.background.bgColor2;
        bgColor1 = _storeTheme.ROOTSTYLE.navbar.bgColor1;
        bgColor2 = _storeTheme.ROOTSTYLE.navbar.bgColor2;
        bgColor = `linear-gradient(${_storeTheme.COLORMAIN1},${_storeTheme.COLORMAIN2})`;
        setLogo(_storeTheme.LOGO);
        setBgColor(bgColor);
        setBgMainColor(bgColor0);
        setTheme(_storeTheme.THEME);
        setHomeUrl(_storeTheme.HOMEURL);
        document.body.style.background = _storeTheme.COLORBG;
      }
    }
  };

  const setUpTheme = async () => {
    renderTheme();
    await axios({
      url: `${appConfig.REACT_APP_APIURL}${getTheme}`,
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
      },
    }).then((res) => {
      const result = res.data.result;
      bgColor0 = result.ROOTSTYLE.background.bgColor2;
      bgColor1 = result.ROOTSTYLE.navbar.bgColor1;
      bgColor2 = result.ROOTSTYLE.navbar.bgColor2;
      bgColor = `linear-gradient(${result.COLORMAIN1},${result.COLORMAIN2})`;
      //console.log(result);
      setLogo(result.LOGO);
      setBgColor(bgColor);
      setBgColor2(bgColor2);
      setBgMainColor(bgColor0);
      setTheme(result.THEME);
      setHomeUrl(result.HOMEURL);
      document.body.style.background = result.COLORBG;

      let systemName = result.SYSTEMNAME.trim();
      _webname = localStorage.getItem("webname");

      if (_webname !== systemName) {
        localStorage.setItem("webname", systemName);
        _webname = systemName;

        renderTheme();
      }
      localStorage.setItem(`initialTheme${_webname}`, JSON.stringify(result));
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const body = {
      username: username,
      password: password,
    };
    if (username !== "" && password !== "") {
      await axios({
        url: `${appConfig.REACT_APP_APIURL}${loginAuth}`,
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
        },
        data: body,
      })
        .then((res) => {
          const result = {
            ...res.data,
            welcomemodal: true,
          };
          if ("accessToken" in result) {
            setIsLoading(true);
            localStorage.setItem("accessToken", result["accessToken"]);
            localStorage.setItem("user", JSON.stringify(result));
            localStorage.setItem("tour", JSON.stringify(true));
            
            // if (theme !== "2") {
            //   window.location.href = "/";
            // } else {
            //   window.location.href = "/Gamelist";
            // }
          } else {
            swal.fire("ข้อความ!", alertText[1]);
          }
        })
        .catch((err) => {
          const resp = err.response.data;
          swal.fire("ข้อความ!", alertText[1]);
        });
    } else {
      swal.fire("ข้อความ!", alertText[0]);
    }
  };

  return (
    <div
      className="container text-center"
      style={{ padding: "4.2rem 10px 0 10px" }}
    >
      <div
        className="col-md-4 mx-auto"
        style={{
          opacity: isLoading ? 0.4 : "unset",
          pointerEvents: isLoading ? "none" : "unset",
        }}
      >
        <div className="myform" style={{ background: getBgColor }}>
          <div className="logo mb-3">
            <div className="col-md-12 text-center">
              <img
                src={process.env.REACT_APP_IMGURL + "/" + getLogo}
                alt="Bigwin"
                style={{ maxWidth: "100%", marginBottom: "15px" }}
              />
            </div>
          </div>
          <form noValidate onSubmit={handleSubmit}>
            <div className="form-group">
              <InputGroup className="mb-3">
                <InputGroup.Text id="basic-addon1">
                  <Icon.PersonFill />
                </InputGroup.Text>
                <FormControl
                  id="email"
                  name="email"
                  placeholder="เบอร์โทรศัพท์"
                  aria-label="email"
                  aria-describedby="basic-addon1"
                  onChange={(e) => setUserName(e.target.value)}
                />
              </InputGroup>
            </div>
            <div className="form-group">
              <InputGroup className="mb-3">
                <InputGroup.Text id="basic-addon1">
                  <Icon.KeyFill />
                </InputGroup.Text>
                <FormControl
                  id="password"
                  name="password"
                  label="รหัสผ่าน"
                  type={showPassword ? "password" : "text"}
                  placeholder="รหัสผ่าน"
                  aria-label="password"
                  aria-describedby="basic-addon1"
                  onChange={(e) => setPassword(e.target.value)}
                />
                <InputGroup.Text id="basic-addon1" onClick={handleShowPassword}>
                  {showPassword ? <Icon.EyeSlash /> : <Icon.Eye />}
                </InputGroup.Text>
              </InputGroup>
            </div>
            <div className="form-group">
              <p className="text-right">
                <a href="#" onClick={handleShow} className="text-white">
                  ลืมรหัสผ่าน ?
                </a>
              </p>
            </div>
            <div className="col-md-12 text-center">
              <button type="submit" className="btn-login mybtn">
                เข้าสู่ระบบ
              </button>
            </div>
            <div className="col-md-12 text-center">
              <div className="login-or">
                <hr className="hr-or" />
                <span className="span-or" style={{ background: getBgColor2 }}>
                  หรือยังไม่มีบัญชี
                </span>
              </div>
            </div>
            <div className="col-md-12 mb-3">
              <p className="text-center">
                <a href="./register" className="btn btn-blu-grow">
                  สมัครสมาชิก
                </a>
              </p>
            </div>
            <div className="form-group">
              <p className="text-center">
                <a href={homeUrl ? homeUrl : "#"} style={{ color: "#aaa" }}>
                  <Icon.Arrow90degLeft /> กลับไปหน้าแรก
                </a>
              </p>
            </div>
          </form>
        </div>
      </div>
      <Modal
        show={show}
        setShow={setShow}
        handleClose={handleClose}
        type={"forgotPassword"}
      />
      <Spinner
        animation="border"
        variant="warning"
        style={{
          position: "absolute",
          display: isLoading ? "unset" : "none",
          color: "primary.main",
        }}
      />
    </div>
  );
};
export default Login;
