import React, { useState, useEffect } from "react";
import Spinner from "react-bootstrap/Spinner";

import preval from "preval.macro";
import moment from "moment";

import Home from "../../../modules/Wallet/Home/Home";
import Profile from "../../../modules/Wallet/Profile/Profile";
import Promotion from "../../../modules/Wallet/Promotion/Promotion";
import History from "../../../modules/Wallet/History/History";
import Recommend from "../Recommend/Index";
import Header from "../../../Header";

import { setUpProFile, fecthGetTheme, initialTheme } from "../../../utils/lib";

export const Layout = () => {
  const dateLogin = localStorage.getItem("dateLogin")
    ? localStorage.getItem("dateLogin")
    : "";
  const [isPage, setIsPage] = useState(true);
  const [activeMenu, setActiveMenu] = useState("home");
  // const [config, setConfig] = useState([]);
  const [initialValueProfile, setInitialValueProfile] = useState(
    //   {
    //   accountname: "",
    //   accountnumber: "",
    //   bankcode: "",
    //   bankname: "",
    //   createdate: "",
    //   lineid: "",
    //   logindate: "",
    //   memberusername: "",
    //   prostatus: "",
    //   tutorial: "",
    // }
    ""
  );

  useEffect(() => {
    fecthGetTheme();
    setUpProFile(setInitialValueProfile);
  }, []);

  return (
    <div
      style={{
        backgroundImage: initialTheme
          ? `url(${process.env.REACT_APP_IMGURL}/${initialTheme.BG})`
          : "#ffffff",
        // backgroundSize: "cover",
        backgroundSize: "100% 100%",
        backgroundPosition: "center center",
        maxWidth: "500px",
        minHeight: "100vh",
        margin: "auto",
        // backgroundAttachment: "fixed",
      }}
    >
      <Header setActiveMenu={setActiveMenu} initialTheme={initialTheme} />
      {activeMenu === "home" && initialValueProfile ? (
        <Home
          dateLogin={dateLogin}
          setActiveMenu={setActiveMenu}
          initialTheme={initialTheme}
          initialValue={initialValueProfile}
          setInitialValue={setInitialValueProfile}
          isPage={isPage}
        />
      ) : (
        <>
          <Spinner
            animation="border"
            variant="warning"
            style={{
              position: "absolute",
              display: !isPage ? "none" : "unset",
              color: "primary.main",
            }}
          />
        </>
      )}
      <div className="page_default_set">
        {activeMenu === "profile" && (
          <Profile
            initialTheme={initialTheme}
            initialValue={initialValueProfile}
            setInitialValue={setInitialValueProfile}
            setIsPage={setIsPage}
          />
        )}
        {activeMenu === "promotion" && (
          <Promotion setIsPage={setIsPage} />
        )}
        {activeMenu === "history" && <History setIsPage={setIsPage} />}
        {activeMenu === "recommend" && (
          <Recommend initialTheme={initialTheme} setIsPage={setIsPage} />
        )}
      </div>

    </div>
  );
};
export default Layout;
