import React, { useState, useEffect } from "react";

import MenuList from "./MenuList";
import Recommend from "./Recommend";
import Income from "./Income";
import Member from "./Member";
import Stat from "./Stat";
import Deposit from "./Deposit";
import Cashback from "./Cashback";

// import { checkLastLogin } from "../../../utils/lib";

export default function Index({ initialTheme, defaultMenu, setIsPage }) {
  const menu = !defaultMenu ? "recommend" : defaultMenu;

  const [activeMenuRecom, setActiveMenuRecom] = useState(menu);
  const [afflink, setAfflink] = useState();
  const [affiliateStatus, setAffiliateStatus] = useState("N");
  const [cashbackStatus, setCashbackStatus] = useState("N");

  useEffect(() => {
    setAfflink(initialTheme.AFURL);
    setIsPage(false);
  }, []);

  return (
    <>
      <MenuList
        active={activeMenuRecom}
        setActiveMenuRecom={setActiveMenuRecom}
        affiliateStatus={affiliateStatus}
        cashbackStatus={cashbackStatus}
      />
      <div
        id="main_content_income"
        className="container transaction_info fade-in"
      >
        <div className="row x-category-index">
          <div className="col-sm-12 col-lg-12 col-12 recommend_section">
            {activeMenuRecom === "recommend" && (
              <Recommend
                // dateLogin={dateLogin}
                Affiliate={afflink}
                setAffiliateStatus={setAffiliateStatus}
                setCashbackStatus={setCashbackStatus}
              />
            )}
            {activeMenuRecom === "income" && (
              <Income
                // dateLogin={dateLogin}
                setAffiliateStatus={setAffiliateStatus}
                setCashbackStatus={setCashbackStatus}
              />
            )}
            {activeMenuRecom === "member" && (
              <Member
                // dateLogin={dateLogin}
                setAffiliateStatus={setAffiliateStatus}
                setCashbackStatus={setCashbackStatus}
              />
            )}
            {activeMenuRecom === "stat" && (
              <Stat
                // dateLogin={dateLogin}
                setAffiliateStatus={setAffiliateStatus}
                setCashbackStatus={setCashbackStatus}
              />
            )}
            {activeMenuRecom === "deposit" && (
              <Deposit
                // dateLogin={dateLogin}
                setAffiliateStatus={setAffiliateStatus}
                setCashbackStatus={setCashbackStatus}
              />
            )}
            {activeMenuRecom === "cashback" && (
              <Cashback
                // dateLogin={dateLogin}
                setAffiliateStatus={setAffiliateStatus}
                setCashbackStatus={setCashbackStatus}
              />
            )}
          </div>
        </div>
      </div>
    </>
  );
}
