import React from "react";

const Header = ({ setActiveMenu, initialTheme }) => {
  var bgColor = "";
  if (initialTheme) {
    const bgColor1 = initialTheme.ROOTSTYLE.navbar.bgColor1;
    const bgColor2 = initialTheme.ROOTSTYLE.navbar.bgColor2;
    bgColor = `linear-gradient(${bgColor1},${bgColor2})`;
  }

  return (
    <div id="header-page" style={{ background: bgColor }}>
      <div className="row no-gutters">
        <div className="col text-center">
          <button
            type="button"
            onClick={() => setActiveMenu("home")}
            className="nonbtn"
          >
            {initialTheme && (
              <img
                src={process.env.REACT_APP_IMGURL + "/" + initialTheme.LOGO}
                className="img-logo"
                alt="logo"
              />
            )}
          </button>
        </div>
      </div>
    </div>
  );
};
export default Header;
