/* eslint-disable no-script-url */
/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable react/jsx-no-target-blank */
import React, { useState, useEffect } from "react";
import axios from "axios";

import Modal from "../../../components/Modal";
import { appConfig, redirActivity } from "../../../utils/lib";
import { getRecommend } from "../../../utils/apiPath";

export const PackageFree = ({
  setActiveMenu,
  setShow,
  initialTheme,
  initialValue,
}) => {
  const user = JSON.parse(localStorage.getItem("user"));

  const [show2, setShow2] = useState(false);
  const [modalType, setModalType] = useState("");
  const [affiliateStatus, setAffiliateStatus] = useState("N");
  const [cashbackStatus, setCashbackStatus] = useState("N");

  const onClickMenu = (menu) => {
    setActiveMenu(menu);
    setShow(false);
  };

  const setUpRecommend = async () => {
    const body = { memberid: user.memberid };
    await axios({
      url: `${appConfig.REACT_APP_APIURL}${getRecommend}`,
      method: "POST",
      timeout: 1000 * 7,
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
      },
      data: body,
    })
      .then((res) => {
        let result = res.data.result;
        setAffiliateStatus(result.affiliateInfo.activestatus);
        setCashbackStatus(result.cashbackInfo[5].configvalue);
      })
      .catch((err) => {
        console.log("error", err);
      });
  };

  const handleClose = () => setShow2(false);
  const handleShow = (modalType) => {
    setShow2(true);
    setModalType(modalType);
  };

  useEffect(() => {
    setUpRecommend();
  }, []);

  const { WELCOMEMODAL, AISTATUS } = initialTheme || "";

  return (
    <>
      <div className="modal-body" style={{ padding: "1rem 0rem" }}>
        <div className="row">
          <div
            className={`col-4 text-center ${
              affiliateStatus === "N" ? "offtab" : ""
            }`}
          >
            <button
              type="button"
              className="btn-none nav-link"
              onClick={() => onClickMenu("recommend")}
            >
              <img
                src={
                  window.location.origin +
                  "/assets/images/icon_modal/ic_recommend.png"
                }
                alt="menu-icon-slot"
                className="img-fluid"
              />
              <p className="text-white">รับทรัพย์</p>
            </button>
          </div>

          <div className="col-4 text-center">
            <button
              type="button"
              className="btn-none nav-link"
              onClick={() => onClickMenu("promotion")}
            >
              <img
                src={
                  window.location.origin +
                  "/assets/images/icon_modal/ic_promotion.png"
                }
                alt="menu-icon-slot"
                className="img-fluid"
              />
              <p className="text-white">โปรโมชั่น</p>
            </button>
          </div>
          {AISTATUS || AISTATUS !== "N" ? (
            <div className="col-4 text-center">
              <button
                type="button"
                className="btn-none nav-link"
                onClick={() => handleShow("ai")}
              >
                <img
                  src={
                    window.location.origin +
                    "/assets/images/icon_modal/ic_ai.png"
                  }
                  alt="menu-icon-slot"
                  className="img-fluid"
                />
                <p className="text-white">สูตร AI</p>
              </button>
            </div>
          ) : (
            <></>
          )}
          <div className="col-4 text-center">
            <button
              type="button"
              className="btn-none nav-link"
              onClick={() => handleShow("annou")}
            >
              <img
                src={
                  window.location.origin +
                  "/assets/images/icon_modal/ic_publish.png"
                }
                alt="menu-icon-slot"
                className="img-fluid"
              />
              <p className="text-white">ประกาศ</p>
            </button>
          </div>

          <div className="col-4 text-center">
            <a
              onClick={() => redirActivity()}
              className="nav-link active"
              target="_blank"
            >
              <img
                src={
                  window.location.origin +
                  "/assets/images/icon_modal/ic_activity.png"
                }
                alt="menu-icon-slot"
                className="img-fluid"
              />
              <p className="text-white">กิจกรรม</p>
            </a>
          </div>

          <div
            className={`col-4 text-center ${
              cashbackStatus === "N" ? "offtab" : ""
            }`}
          >
            <button
              type="button"
              className="btn-none nav-link"
              onClick={() => onClickMenu("cashback")}
            >
              <img
                src={
                  window.location.origin +
                  "/assets/images/icon_modal/ic_cashback.png"
                }
                alt="menu-icon-slot"
                className="img-fluid"
              />
              <p className="text-white">คืนยอดเสีย</p>
            </button>
          </div>
        </div>
      </div>
      <Modal
        setActiveMenu={setActiveMenu}
        show={show2}
        setShow={setShow2}
        handleClose={handleClose}
        type={modalType}
        initialTheme={initialTheme}
        initialValue={initialValue}
      />
    </>
  );
};
export default PackageFree;
