import React, { useState, useEffect } from "react";

import axios from "axios";
import Spinner from "react-bootstrap/Spinner";

import { promotionList } from "../../../utils/apiPath";
import { appConfig, checkLastLogin } from "../../../utils/lib";

export default function Promotion({ setIsPage }) {
  const [promotionShow, setPromotion] = useState("");
  const [isLoading, setIsLoading] = useState(true);

  const promotionListMain = async () => {
    await axios({
      url: `${appConfig.REACT_APP_APIURL}${promotionList}`,
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
      },
    }).then((res) => {
      const result = res.data.result.rows;
      setPromotion(result);
      setIsLoading(false);
    });
  };

  useEffect(() => {
    promotionListMain();
    checkLastLogin();
    setIsPage(false);
  }, []);

  const PromotionList = ({ list }) => {
    return (
      <>
        {list.length > 0 &&
          list.map((item, index) => {
            return (
              <div
                className="col-md-6"
                style={{ marginBottom: "25px" }}
                key={index}
              >
                <div
                  className="card"
                  style={{ borderRadius: "0px 0px 15px 15px" }}
                >
                  <img
                    src={appConfig.REACT_APP_IMGURL + "/" + item.imgurl}
                    alt=""
                    className="img-fluid"
                  />
                  <div className="card-body">
                    <p className="card-text" style={{ color: "#000" }}>
                      {item.name}
                    </p>
                  </div>
                </div>
              </div>
            );
          })}
      </>
    );
  };

  return (
    <div
      className="container transaction_info fade-in mt-4"
      // style={{ marginTop: "90px" }}
    >
      <div
        className="row"
        style={{
          opacity: isLoading ? 0.4 : "unset",
          pointerEvents: isLoading ? "none" : "unset",
        }}
      >
        <h4 className="text-center text-highlight mb-3">
          <img
            src={window.location.origin + "/assets/images/ic_promotion.png"}
            style={{ display: "inline-block", width: "25px" }}
            alt=""
          />{" "}
          รายการโปรโมชั่น
        </h4>
        <div className="col-12">
          <hr
            style={{
              borderColor: "#c8a546",
              borderBottom: " solid 1px #efaf3e",
            }}
          />
          <div className="row">
            <PromotionList list={promotionShow} />
          </div>
        </div>
      </div>
      <Spinner
        animation="border"
        variant="warning"
        style={{
          position: "absolute",
          display: isLoading ? "unset" : "none",
          color: "primary.main",
        }}
      />
    </div>
  );
}
