import React, { useState } from "react";
import axios from "axios";

import swal from "sweetalert2";
import * as Icon from "react-bootstrap-icons";

import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";

import { forgotPassword } from "../../../utils/apiPath";
import { appConfig } from "../../../utils/lib";

export const ForgotPassword = ({ setShow }) => {
  const token = localStorage.getItem("accessToken");
  const [memberPhone, setMemberPhone] = useState("");
  const [accountNumber, setAccountNumber] = useState("");
  const [password1, setPassword1] = useState("");
  const [password2, setPassword2] = useState("");

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (password1.length < 6 || password2.length < 6) {
      swal.fire("ข้อความ!", "กรุณากรอกรหัสผ่านอย่างน้อย 6 ตัวอักษรค่ะ");
    } else {
      if (password1 !== password2) {
        swal.fire("ข้อความ!", "รหัสผ่านไม่ตรงกันค่ะ");
      } else {
        const body = {
          memberphone: memberPhone,
          accountnumber: accountNumber,
          password: password1,
        };
        await axios({
          url: `${appConfig.REACT_APP_APIURL}${forgotPassword}`,
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            "x-access-token": token,
            Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
          },
          data: body,
        }).then((res) => {
          const result = res.data;
          if (result.status === "done") {
            setShow(false);
            swal.fire("ข้อความ!", result.msg);
          } else if (result.status === "fail") {
            swal.fire("ข้อความ!", result.msg);
          }
        });
      }
    }
  };
  return (
    <>
      <form id="frm-forgetpass" name="frm-forgetpass" onSubmit={handleSubmit}>
        <div className="modal-body msformforgot">
          <p className="text-center">กรุณากรอกเบอร์โทรศัพท์และเลขที่บัญชี</p>
          <div className="myform" style={{ padding: "0", background: "none" }}>
            <div className="form-group ">
              <InputGroup className="mb-3">
                <InputGroup.Text id="basic-addon1">
                  <Icon.PersonFill />
                </InputGroup.Text>
                <FormControl
                  id="memberPhone"
                  name="memberPhone"
                  placeholder="เบอร์โทรศัพท์"
                  type="number"
                  onChange={(e) => setMemberPhone(e.target.value)}
                />
              </InputGroup>
            </div>
            <div className="form-group ">
              <InputGroup className="mb-3">
                <InputGroup.Text id="basic-addon1">
                  <Icon.CardText />
                </InputGroup.Text>
                <FormControl
                  id="accountNumber"
                  name="accountNumber"
                  placeholder="เลขที่บัญชี"
                  type="number"
                  onChange={(e) => setAccountNumber(e.target.value)}
                />
              </InputGroup>
            </div>

            <p className="text-center">กรุณากรอกรหัสผ่านใหม่</p>

            <div className="form-group">
              <InputGroup className="mb-3">
                <InputGroup.Text id="basic-addon1">
                  <Icon.KeyFill />
                </InputGroup.Text>
                <FormControl
                  type="password"
                  id="password"
                  name="password"
                  placeholder="รหัสผ่านใหม่"
                  onChange={(e) => setPassword1(e.target.value)}
                />
              </InputGroup>
            </div>
            <div className="form-group">
              <InputGroup className="mb-3">
                <InputGroup.Text id="basic-addon1">
                  <Icon.KeyFill />
                </InputGroup.Text>
                <FormControl
                  type="password"
                  id="con_newpassword"
                  name="con_newpassword"
                  placeholder="ยืนยันรหัสผ่านใหม่"
                  onChange={(e) => setPassword2(e.target.value)}
                />
              </InputGroup>
            </div>
          </div>

          <div className="modal-footer" style={{ borderTop: "none" }}>
            <button
              type="submit"
              className=" action-button btn btn-block mybtn icon  btn_login tx-tfm"
            >
              ยืนยัน
            </button>
          </div>
        </div>
      </form>
    </>
  );
};
export default ForgotPassword;
