import React, { useEffect, useState } from "react";
import { authenLogin, setUpLastLogin } from "../../../utils/lib";

export const RegisterSuccess = ({ initialTheme, initialValue, logo }) => {
  const [isLoading, setIsLoading] = useState(false);

  // const { THEME } = initialTheme || "";
  var THEME = "";
  if (initialTheme) {
    THEME = initialTheme.THEME;
  } else {
    THEME = "";
  }

  useEffect(() => {
    if (isLoading) {
      setUpLastLogin(THEME);
    }
  }, [isLoading]);

  return (
    <>
      <div
        className="modal-body"
        style={{ textAlign: "center", paddingBottom: "0" }}
      >
        <img src={logo} style={{ maxWidth: "100%", marginBottom: "15px" }} />
        <h1 className="text-center text-yellow">Register Success</h1>
        <h1 className="text-center text-yellow" style={{ fontWeight: 500 }}>
          การสมัครของคุณสำเร็จแล้ว
        </h1>
        <h5
          className="text-center"
          style={{ fontWeight: 300, lineHeight: "32px", marginTop: "20px" }}
        >
          ขอบคุณสำหรับการสมัคร คุณสามารถใช้ ชื่อเข้าระบบ และ รหัสผ่าน
          ด้านล่างที่ตั้งไว้เพื่อเข้าสู่ระบบ
        </h5>
        <div className="bg_register_user text-left">
          <h4 style={{ color: "#000" }}>
            ชื่อเข้าระบบ:{" "}
            <span id="txt_username">{initialValue.memberusername}</span>
          </h4>
          <h4 style={{ color: "#000" }}>
            รหัสผ่าน:{" "}
            <span id="txt_password">{initialValue.memberpassword}</span>
          </h4>
        </div>
      </div>
      <div
        className="modal-footer"
        style={{
          display: "block",
          textAlign: "center",
          border: "none",
          paddingBottom: "30px",
        }}
      >
        <button
          // href="./"
          style={{ display: "block", width: "50%", margin: "auto" }}
          className="google btn mybtn "
          onClick={() => authenLogin(initialValue, setIsLoading)}
        >
          เข้าสู่ระบบ
        </button>
      </div>
    </>
  );
};
export default RegisterSuccess;
