import React, { useState, useEffect } from "react";
import axios from "axios";
import Spinner from "react-bootstrap/Spinner";

import { appConfig, checkLastLogin } from "../../../utils/lib";
import { getMember } from "../../../utils/apiPath";
import Pagination from "../../../components/Pagination";

export const MemberList = ({ list }) => {
  return (
    <>
      {list && list.length > 0 ? (
        list.map((item, index) => {
          return (
            <tr key={index}>
              <td className="text-center">{item.no}</td>
              <td className="text-center">{item.memberusername}</td>
              <td className="text-center">{item.createdat}</td>
            </tr>
          );
        })
      ) : (
        <tr>
          <td colSpan="5" className="text-center">
            ไม่พบข้อมูล
          </td>
        </tr>
      )}
    </>
  );
};

export default function Member({
  dateLogin,
  setAffiliateStatus,
  setCashbackStatus,
}) {
  const user = JSON.parse(localStorage.getItem("user"));
  const [memberValue, setMemberValue] = useState({ memberList: [] });
  const [isLoading, setIsLoading] = useState(true);
  const { memberList } = memberValue;

  const [currentPage, setCurrentPage] = useState(1);
  const [dataPerPage] = useState(10);

  const setUpMember = async () => {
    const body = { memberid: user.memberid };
    await axios({
      url: `${appConfig.REACT_APP_APIURL}${getMember}`,
      method: "POST",
      timeout: 1000 * 7,
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
      },
      data: body,
    })
      .then((res) => {
        setIsLoading(false);
        let result = res.data.result;
        setMemberValue(result);
        setAffiliateStatus(result.affiliateInfo.activestatus);
        setCashbackStatus(result.cashbackInfo[5].configvalue);
      })
      .catch((err) => {
        setIsLoading(false);
        console.log("error", err);
      });
  };

  useEffect(() => {
    setUpMember();
    checkLastLogin();
  }, []);

  // Get current data
  const indexOfLastPost = currentPage * dataPerPage;
  const indexOfFirstPost = indexOfLastPost - dataPerPage;
  const currentData = memberList.slice(indexOfFirstPost, indexOfLastPost);

  // Change page
  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  return (
    <>
      {!isLoading && (
        <>
          <h3
            className="text-center mb-3 fade-in"
            style={{ fontWeight: "500" }}
          >
            สมาชิก
          </h3>
          <div className="row no-gutters">
            <table className="table table-bordered shadow-sm ">
              <thead>
                <tr>
                  <th className="text-center">ลำดับ</th>
                  <th className="text-center">ยูสเซอร์เนม</th>
                  <th className="text-center">วันที่สมัคร</th>
                </tr>
              </thead>
              <tbody>
                <MemberList list={currentData} />
              </tbody>
            </table>
            <Pagination
              dataPerPage={dataPerPage}
              totalData={memberList.length}
              currentPage={currentPage}
              paginate={paginate}
            />
            <div>
              <small>
                ** รายการสรุป การเล่นของสมาชิกที่มีการเล่น <br /> &nbsp;&nbsp;
                ตั้งแต่วันที่ 18/05/2022 เวลา 11:00 น. จนถึงวันที่ 19/05/2022
                เวลา 11:00 น.
              </small>
            </div>
          </div>
        </>
      )}
      <Spinner
        animation="border"
        variant="warning"
        style={{
          position: "absolute",
          display: isLoading ? "unset" : "none",
          color: "primary.main",
        }}
      />
    </>
  );
}
