import React, { useState, useEffect } from "react";
import axios from "axios";

import Nav from "react-bootstrap/Nav";
import Fade from "react-bootstrap/Fade";
import Spinner from "react-bootstrap/Spinner";

import { appConfig, checkLastLogin } from "../../../utils/lib";
import { getIncome } from "../../../utils/apiPath";

export default function Income({
  dateLogin,
  setAffiliateStatus,
  setCashbackStatus,
}) {
  const user = JSON.parse(localStorage.getItem("user"));
  const [isLoading, setIsLoading] = useState(true);
  const [incomeValue, setIncomeValue] = useState("");
  const [active, setActive] = useState("");

  const setDefaultTap = (depositstatus, winlossstatus, turnoverstatus) => {
    if (turnoverstatus === "Y") {
      setActive("tab-com");
    }
    if (winlossstatus === "Y") {
      setActive("tab-winloss");
    }
    if (depositstatus === "Y") {
      setActive("tab-deposit");
    }
  };

  const setUpIncome = async () => {
    const body = { memberid: user.memberid };
    await axios({
      url: `${appConfig.REACT_APP_APIURL}${getIncome}`,
      method: "POST",
      timeout: 1000 * 7,
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
      },
      data: body,
    })
      .then((res) => {
        setIsLoading(false);
        let result = res.data.result;
        result = {
          ...result,
          income: result.income.toFixed(2),
          canwithdraw: result.canwithdraw.toFixed(2),
          winlosscom: result.winlosscom.toFixed(2),
          depositcom: result.depositcom.toFixed(2),
          wlgamecom: result.wlgamecom.toFixed(2),
          depositspercent: Number(result.depositspercent).toFixed(2),
          winlosspercent: Number(result.winlosspercent).toFixed(2),
          turnovercasino: Number(result.turnovercasino).toFixed(2),
          turnoverfootball: Number(result.turnoverfootball).toFixed(2),
          turnovergame: Number(result.turnovergame).toFixed(2),
          turnoverlotto: Number(result.turnoverlotto).toFixed(2),
          turnoverm2: Number(result.turnoverm2).toFixed(2),
          turnovermultiplay: Number(result.turnovermultiplay).toFixed(2),
          turnoverparlay: Number(result.turnoverparlay).toFixed(2),
          turnoveresport: Number(result.turnoveresport).toFixed(2),
          turnoverstep: Number(result.turnoverstep).toFixed(2),
        };
        setAffiliateStatus(result.affiliateInfo.activestatus);
        setCashbackStatus(result.cashbackInfo[5].configvalue);
        setIncomeValue(result);
        setDefaultTap(
          result.affiliateInfo.depositstatus,
          result.affiliateInfo.winlossstatus,
          result.affiliateInfo.turnoverstatus
        );
      })
      .catch((err) => {
        setIsLoading(false);
        console.log("error", err);
      });
  };

  const {
    income,
    canwithdraw,
    depositcom,
    depositspercent,
    winlosscom,
    winlosspercent,
    wlgamecom,
    turnovercasino,
    turnoverfootball,
    turnovergame,
    turnoverlotto,
    turnoverm2,
    turnovermultiplay,
    turnoverparlay,
    turnoveresport,
    turnoverstep,
    affiliateInfo,
  } = incomeValue || "";

  const { depositstatus, winlossstatus, turnoverstatus } = affiliateInfo || "";
  const cssTable = { width: "30%", backgroundColor: "#101027" };

  useEffect(() => {
    setUpIncome();
    checkLastLogin();
  }, []);

  return (
    <>
      {!isLoading && (
        <>
          <h3 className="text-center mb-3" style={{ fontWeight: "500" }}>
            รายได้
          </h3>
          <div className="row no-gutters fade-in">
            <div className="col-6">
              <div className="card-report text-center p-0">
                <p className="txt_head_income">รายได้ทั้งหมด</p>
                <p className="mb-0 text-highlight" style={{ padding: "8px" }}>
                  {income}
                </p>
              </div>
            </div>
            <div className="col-6">
              <div className="card-report text-center p-0">
                <p className="txt_head_income">รายได้ที่ถอนได้</p>
                <p className="mb-0 text-highlight" style={{ padding: "8px" }}>
                  {canwithdraw}
                </p>
              </div>
            </div>
            <div className="col-12 mt-4 ">
              <Nav
                className="nav nav-pills mb-3"
                id="income-tab"
                role="tablist"
                activeKey={active}
                onSelect={(selectedKey) => setActive(selectedKey)}
              >
                {depositstatus === "Y" && (
                  <Nav.Item className="nav-item">
                    <Nav.Link eventKey="tab-deposit">ฝาก</Nav.Link>
                  </Nav.Item>
                )}
                {winlossstatus === "Y" && (
                  <Nav.Item className="nav-item">
                    <Nav.Link eventKey="tab-winloss">แพ้ / ชนะ</Nav.Link>
                  </Nav.Item>
                )}
                {turnoverstatus === "Y" && (
                  <Nav.Item className="nav-item">
                    <Nav.Link eventKey="tab-com">ค่าคอม</Nav.Link>
                  </Nav.Item>
                )}
              </Nav>
              <div className="tab-content" id="pills-tabContent  ">
                <Fade
                  in={active === "tab-deposit" ? true : false}
                  style={{ display: active === "tab-deposit" ? "" : "none" }}
                >
                  <table className="table table-bordered shadow-sm">
                    <tbody>
                      <tr>
                        <td style={cssTable}>รายได้</td>
                        <td className="text-center text-highlight">
                          {depositcom}{" "}
                        </td>
                      </tr>
                      <tr>
                        <td style={cssTable}>เปอร์เซ็นต์</td>
                        <td className="text-center text-highlight">
                          {depositspercent} %{" "}
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </Fade>

                <Fade
                  in={active === "tab-winloss" ? true : false}
                  style={{ display: active === "tab-winloss" ? "" : "none" }}
                >
                  <table className="table table-bordered shadow-sm">
                    <tbody>
                      <tr>
                        <td style={cssTable}>รายได้</td>
                        <td className="text-center text-highlight">
                          {winlosscom}{" "}
                        </td>
                      </tr>
                      <tr>
                        <td style={cssTable}>เปอร์เซ็นต์</td>
                        <td className="text-center text-highlight">
                          {winlosspercent} %{" "}
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </Fade>

                <Fade
                  in={active === "tab-com" ? true : false}
                  style={{ display: active === "tab-com" ? "" : "none" }}
                >
                  <table className="table table-bordered shadow-sm">
                    <tbody>
                      <tr>
                        <td style={cssTable}>รายได้</td>
                        <td className="text-center text-highlight">
                          {wlgamecom}
                        </td>
                      </tr>
                      <tr>
                        <td style={cssTable}>เปอร์เซ็นต์ Casino</td>
                        <td className="text-center text-highlight">
                          {turnovercasino}
                        </td>
                      </tr>
                      <tr>
                        <td style={cssTable}>เปอร์เซ็นต์ Football</td>
                        <td className="text-center text-highlight">
                          {turnoverfootball}
                        </td>
                      </tr>

                      <tr>
                        <td style={cssTable}>เปอร์เซ็นต์ Game</td>
                        <td className="text-center text-highlight">
                          {turnovergame}
                        </td>
                      </tr>

                      <tr>
                        <td style={cssTable}>เปอร์เซ็นต์ Lotto</td>
                        <td className="text-center text-highlight">
                          {turnoverlotto}
                        </td>
                      </tr>

                      <tr>
                        <td style={cssTable}>เปอร์เซ็นต์ M2</td>
                        <td className="text-center text-highlight">
                          {turnoverm2}
                        </td>
                      </tr>

                      <tr>
                        <td style={cssTable}>เปอร์เซ็นต์ Multiplayer</td>
                        <td className="text-center text-highlight">
                          {turnovermultiplay}
                        </td>
                      </tr>

                      <tr>
                        <td style={cssTable}>เปอร์เซ็นต์ Parlay</td>
                        <td className="text-center text-highlight">
                          {turnoverparlay}
                        </td>
                      </tr>

                      <tr>
                        <td style={cssTable}>เปอร์เซ็นต์ Sport</td>
                        <td className="text-center text-highlight">
                          {turnoveresport}
                        </td>
                      </tr>

                      <tr>
                        <td style={cssTable}>เปอร์เซ็นต์ Step</td>
                        <td className="text-center text-highlight">
                          {turnoverstep}
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </Fade>
              </div>
            </div>
          </div>
        </>
      )}
      <Spinner
        animation="border"
        variant="warning"
        style={{
          position: "absolute",
          display: isLoading ? "unset" : "none",
          color: "primary.main",
        }}
      />
    </>
  );
}
