import React, { useEffect, useState } from "react";
import axios from "axios";
import { v4 as uuidv4 } from "uuid";
import { Base64 } from "js-base64";

import Nav from "react-bootstrap/Nav";
import Fade from "react-bootstrap/Fade";
import * as Icon from "react-bootstrap-icons";
import Spinner from "react-bootstrap/Spinner";
import Form from "react-bootstrap/Form";
import swal from "sweetalert2";

import { getDeposit } from "../../../utils/apiPath";
import {
  appConfig,
  updateStatusPro,
  onClickCopy,
  checkLastLogin,
  logOut,
} from "../../../utils/lib";

export const Deposite = ({
  initialTheme,
  initialValue,
  setInitialValue,
  dateLogin,
}) => {
  const [active, setActive] = useState("tab-0");
  const [bankList, setBankList] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const token = localStorage.getItem("accessToken");
  const { LINEURL } = initialTheme || "";

  useEffect(() => {
    setUpBank();
    checkLastLogin(true);
  }, []);

  const setUpBank = async () => {
    const body = {};

    await axios({
      url: `${appConfig.REACT_APP_APIURL}${getDeposit}`,
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "x-access-token": token,
        Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
      },
      data: body,
    })
      .then((res) => {
        var result = res.data.result;
        var byBank = res?.data?.bybank || "";

        if (byBank.toLowerCase() === appConfig.REACT_APP_PFNAME.toLowerCase()) {
          setBankList(result);
          setIsLoading(false);
        } else {
          swal.fire({
            icon: "error",
            title: "เกิดข้อผิดพลาด",
            text: `เกิดข้อผิดพลาด Respones Code 5002 กรุณาลองใหม่อีกครั้ง!`,
          });
        }
        // console.log(result,'result');
      })
      .catch((err) => {
        if (
          parseInt(err.response.status) === 500 ||
          parseInt(err.response.status) === 501 ||
          parseInt(err.response.status) === 502
        ) {
          swal.fire({
            icon: "error",
            title: "เกิดข้อผิดพลาด",
            text: `เกิดข้อผิดพลาด Respones Code ${err.response.status} กรุณาลองใหม่อีกครั้ง!`,
          });
        } else {
          logOut();
        }
      });
  };

  return (
    <div
      className="modal-body transaction_info"
      style={{
        opacity: isLoading ? 0.4 : "unset",
        pointerEvents: isLoading ? "none" : "unset",
      }}
    >
      <div className="row" id="render_bankinfo" style={{ minHeight: "600px" }}>
        <h4 className="text-center">เลือกธนาคาร</h4>
        <Nav
          className="justify-content-center mb-3"
          variant="pills"
          id="pills-tab"
          activeKey={active}
          onSelect={(selectedKey) => setActive(selectedKey)}
        >
          {bankList.length > 0 &&
            bankList.map((item, index) => {
              return (
                <>
                  <Nav.Item key={uuidv4()}>
                    <Nav.Link eventKey={"tab-" + index}>
                      <img
                        src={
                          window.location.origin +
                          "/assets/images/icon_bank/" +
                          (item.bankcode === "TRUE"
                            ? "truemoney"
                            : item.bankcode.toLowerCase()) +
                          ".jpg"
                        }
                        title={item.bankname}
                        alt={item.bankname}
                      />
                    </Nav.Link>
                  </Nav.Item>
                </>
              );
            })}
        </Nav>

        <div className="tab-content mt-3 mb-5" id="pills-tabContent">
          {bankList.length > 0
            ? bankList.map((item, index) => {
                let banknum = item.accountnumber.replaceAll("-", "");

                return (
                  <>
                    <Fade
                      className={"fade-in"}
                      key={uuidv4()}
                      in={active === "tab-" + index ? true : false}
                      style={{
                        display: active === "tab-" + index ? "" : "none",
                      }}
                    >
                      <div className="col-12">
                        <div className="card-bank">
                          <div className="row">
                            <div className="col-auto">
                              <figure className="avatar avatar-50 active">
                                <img
                                  src={
                                    window.location.origin +
                                    "/assets/images/icon_bank/" +
                                    (item.bankcode === "TRUE"
                                      ? "truemoney"
                                      : item.bankcode.toLowerCase()) +
                                    ".jpg"
                                  }
                                  title={item.accountname}
                                  alt={item.accountname}
                                />
                              </figure>
                            </div>
                            <div className="col pl-0 align-self-center">
                              <p className="mb-0  d-flex">
                                เลขที่บัญชี:&nbsp;
                                <span>{item.accountnumber}</span>
                              </p>
                              <p className="mb-0 ">ธนาคาร: {item.bankname}</p>
                              <p className="mb-0 text-mute ">
                                ชื่อ: {item.accountname}
                              </p>
                            </div>
                          </div>
                          <div className="form-group mb-0 mt-4">
                            <button
                              type="button"
                              className="btn-main btn-block mybtn btn_bg icon tx-tfm"
                              value={banknum}
                              onClick={(e) =>
                                onClickCopy(
                                  e.target.value,
                                  "accountnumber",
                                  bankList
                                )
                              }
                            >
                              <Icon.Files /> คัดลอกเลขบัญชี
                            </button>
                          </div>
                        </div>

                        <div className="bank_user_info mt-4">
                          <p className="text-left mb-1">
                            บัญชีที่ลูกค้าลงทะเบียน
                          </p>
                          <p className="text-left mb-1">
                            ธนาคาร: {initialValue.bankname}
                          </p>
                          <p className="text-left mb-1">
                            เลขที่บัญชี: {initialValue.accountnumber}
                          </p>
                          <p className="text-left">
                            ชื่อบัญชี: {initialValue.accountname}
                          </p>
                        </div>
                      </div>
                    </Fade>
                  </>
                );
              })
            : "-"}
          <div className="col-12">
            <div className="bank_info ">
              {/* <label
                htmlhtmlFor="f-option"
                className="l-radio"
                style={{ marginTop: 0 }}
              >
                <input
                  type="radio"
                  id="f-option"
                  className="btn_prostatus"
                  name="prostatus"
                  value="Y"
                  tabIndex="1"
                  checked={initialValue.prostatus === "Y" ? "checked" : ""}
                />
                <span>รับโบนัส</span>
              </label>
              <label
                htmlhtmlFor="s-option"
                className="l-radio"
                style={{ marginTop: 0 }}
              >
                <input
                  type="radio"
                  id="s-option"
                  className="btn_prostatus"
                  name="prostatus"
                  value="N"
                  tabIndex="2"
                  checked={initialValue.prostatus === "N" ? "checked" : ""}
                />
                <span>ไม่รับโบนัส</span>
              </label> */}
              <label htmlFor="f-option" className="l-radio">
                <Form.Check
                  inline
                  label="รับโบนัส"
                  name="prostatus"
                  type="radio"
                  id="f-option-1"
                  onChange={() =>
                    updateStatusPro("Y", setIsLoading, setInitialValue)
                  }
                  checked={initialValue.prostatus === "Y"}
                />
              </label>
              <label htmlFor="s-option" className="l-radio">
                <Form.Check
                  inline
                  label="ไม่รับโบนัส"
                  name="prostatus"
                  type="radio"
                  id="f-option-2"
                  onChange={() =>
                    updateStatusPro("N", setIsLoading, setInitialValue)
                  }
                  checked={initialValue.prostatus === "N"}
                />
              </label>
            </div>
          </div>

          <div className="col-12">
            <div className="bank_info mt-4 ">
              <h4
                className="text-left text-highlight mb-3"
                style={{ textAlign: "left" }}
              >
                หมายเหตุ
              </h4>
              <p className="text-left text-highlight mb-1">
                - โอนเงินจากบัญชีที่ลงทะเบียนเท่านั้น
              </p>
              <p className="text-left text-highlight mb-1">
                - หลังจากโอนเงิน รอไม่เกิน 1 นาทีในการทำรายการ
              </p>
              <p className="text-left text-highlight">
                - หากพบปัญหาแจ้งโอนหรือติดต่อเจ้าหน้าที่
              </p>
              <p className="text-center mt-4">
                <a
                  href={LINEURL}
                  style={{ color: "#000", textDecoration: "none" }}
                  className="btn-main btn-block mybtn btn_bg icon tx-tfm"
                >
                  <Icon.PeopleFill /> ติดต่อเจ้าหน้าที่
                </a>
              </p>
            </div>
          </div>
        </div>
      </div>
      <Spinner
        animation="border"
        variant="warning"
        style={{
          position: "absolute",
          display: isLoading ? "unset" : "none",
          color: "primary.main",
        }}
      />
    </div>
  );
};
export default Deposite;
