import React, { useState } from "react";
import axios from "axios";

import swal from "sweetalert2";

import { changePass } from "../../../utils/apiPath";
import { appConfig } from "../../../utils/lib";

export default function ChangePassword({ setShow }) {
  const [password1, setPassword1] = useState("");
  const [password2, setPassword2] = useState("");
  const token = localStorage.getItem("accessToken");

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (password1.length < 6 || password2.length < 6) {
      swal.fire("ข้อความ!", "กรุณากรอกรหัสผ่านอย่างน้อย 6 ตัวอักษรค่ะ");
    } else {
      if (password1 != password2) {
        swal.fire("ข้อความ!", "รหัสผ่านไม่ตรงกันค่ะ");
      } else {
        const body = { password: password1 };
        await axios({
          url: `${appConfig.REACT_APP_APIURL}${changePass}`,
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            "x-access-token": token,
            Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
          },
          data: body,
        }).then((res) => {
          const result = res.data.status;
          if (result === "done") {
            setShow(false);
            swal.fire("ข้อความ", "เปลี่ยนรหัสผ่านเรียบร้อยแล้วค่ะ");
          }
        });
      }
    }
  };
  return (
    <>
      <form id="frm-resetpass" name="frm-resetpass" onSubmit={handleSubmit}>
        <div className="modal-body mb-3">
          <div className="myform" style={{ padding: "0", background: "none" }}>
            <input
              type="password"
              className="frm-resetpass mb-3"
              placeholder="รหัสผ่าน"
              id="newspassword"
              name="newspassword"
              onChange={(e) => setPassword1(e.target.value)}
            />
            <input
              type="password"
              className="frm-resetpass"
              placeholder="ยืนยันรหัสผ่าน"
              id="con_newspassword"
              name="con_newspassword"
              onChange={(e) => setPassword2(e.target.value)}
            />
          </div>
        </div>
        <div className="modal-footer" style={{ borderTop: "none" }}>
          <button
            type="submit"
            className="btn-main btn-block mybtn btn_bg icon tx-tfm btn-set-password mt-0"
          >
            ยืนยัน
          </button>
        </div>
      </form>
    </>
  );
}
