import React from "react";
import Modal from "react-bootstrap/Modal";
import Deposite from "./Content/Deposite";
import Withdraw from "./Content/Withdraw";
import Annou from "./Content/Annou";
import ForgotPassword from "./Content/ForgotPassword";
import ChangePassword from "./Content/ChangePassword";
import PackageFree from "./Content/PackageFree";
import RegisterSuccess from "./Content/RegisterSuccess";
import AI from "./Content/AI";

export const Index = ({
  show,
  setShow,
  handleClose,
  type,
  setActiveMenu,
  initialTheme,
  initialValue,
  setInitialValue,
  credit,
  setCredit,
  logo,
  dateLogin,
}) => {
  const title = {
    deposit: "ฝากเงิน",
    withdraw: "ถอนเงิน",
    annou: "ประกาศ",
    forgotPassword: "เปลี่ยนรหัสผ่าน",
    changePassword: "เปลี่ยนรหัสผ่าน",
    packagefree: "แพ็คเกจฟรี",
    ai: "สูตร AI",
  };

  const getBgColor1 = initialTheme?.ROOTSTYLE?.background?.bgColor1 || "";
  const getNavbarColor1 = initialTheme?.ROOTSTYLE?.navbar?.bgColor1 || "";

  return (
    <>
      <Modal show={show} onHide={handleClose}>
        <div
          style={{
            background: `linear-gradient(${getNavbarColor1},${getBgColor1})`,
          }}
        >
          <Modal.Header closeButton>
            <Modal.Title>
              <h5 className="text-highlight">{title[type]}</h5>
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            {type === "deposit" && (
              <Deposite
                initialTheme={initialTheme}
                initialValue={initialValue}
                setInitialValue={setInitialValue}
                dateLogin={dateLogin}
              />
            )}
            {type === "withdraw" && (
              <Withdraw
                initialValue={initialValue}
                credit={credit}
                setCredit={setCredit}
                show={show}
                setShow={setShow}
                dateLogin={dateLogin}
              />
            )}
            {type === "annou" && <Annou initialTheme={initialTheme} />}
            {type === "forgotPassword" && <ForgotPassword setShow={setShow} />}
            {type === "changePassword" && <ChangePassword setShow={setShow} />}
            {type === "registerSuccess" && (
              <RegisterSuccess
                initialTheme={initialTheme}
                initialValue={initialValue}
                logo={logo}
              />
            )}
            {type === "packagefree" && (
              <PackageFree
                setActiveMenu={setActiveMenu}
                setShow={setShow}
                initialTheme={initialTheme}
                initialValue={initialValue}
              />
            )}
            {type === "ai" && (
              <AI
                initialTheme={initialTheme}
                initialValue={initialValue}
                dateLogin={dateLogin}
              />
            )}
          </Modal.Body>
        </div>
      </Modal>
    </>
  );
};
export default Index;
