import React, { useEffect, useState } from "react";
import { useSearchParams } from "react-router-dom";
import { Base64 } from "js-base64";
import { MetaTags } from "react-meta-tags";

import qs from "qs";
import "bootstrap/dist/css/bootstrap.min.css";
import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";
import Form from "react-bootstrap/Form";
import swal from "sweetalert2";
import Spinner from "react-bootstrap/Spinner";
import * as Icon from "react-bootstrap-icons";
import {
  getTheme,
  getBankList,
  CheckPhoneNumber,
  conFirmRegister,
} from "../../utils/apiPath";
import { appConfig, validatePhoneNumber, initialTheme } from "../../utils/lib";
import Modal from "../../components/Modal";

import axios from "axios";
import { SettingsOutlined } from "@material-ui/icons";

const Register = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [memberPhone, setMemberPhone] = useState("");
  const [password1, setPassword1] = useState("");
  const [password2, setPassword2] = useState("");
  const [step1, setStep1] = useState(true);
  const [step2, setStep2] = useState(false);
  const [step3, setStep3] = useState(false);
  const [bankList, setBankList] = useState([]);
  const [memberTempId, setMemberTempId] = useState("");

  const [initialValue, setInitialValue] = useState({
    username: "",
    password: "",
    memberid: "",
    membername: "",
    memberpassword: "",
    memberusername: "",
    memberusername2: "",
    token: "",
  });
  const [bankValue, setBankValue] = useState({
    bankid: "",
    accountname: "",
    accountnumber: "",
    prostatus: "N",
  });
  const [modalSuccess, setModalSuccess] = useState(false);
  const [searchParams, setSearchParams] = useSearchParams();
  const uplineid = searchParams.get("uplineid")
    ? Base64.decode(searchParams.get("uplineid"))
    : 0;

  const handleSubmit = async (e) => {
    e.preventDefault();
    const checkValidate = validatePhoneNumber(memberPhone);

    if (step1) {
      if (checkValidate.status) {
        const body = {
          memberphone: memberPhone,
          uplineid: "",
        };
        await axios({
          url: `${appConfig.REACT_APP_APIURL}${CheckPhoneNumber}`,
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
          },
          data: body,
        })
          .then((res) => {
            const result = res.data;
            if (result.status == "false") {
              swal.fire("ข้อความ!", "ข้อมูล เบอร์โทร มีอยู่แล้วในระบบ");
            } else {
              setStep1(false);
              setStep2(true);
              setMemberTempId(result.membertempid);
            }
          })
          .catch((err) => {
            swal.fire("ข้อความ!", "กรุณาลองใหม่อีกครั้ง");
          });
      } else {
        swal.fire("ข้อความ", checkValidate.msg);
      }
    }
    if (step2) {
      if (password1.length < 6 || password2.length < 6) {
        swal.fire("ข้อความ!", "กรุณากรอกรหัสผ่านอย่างน้อย 6 ตัวอักษรค่ะ");
      } else {
        if (password1 != password2) {
          swal.fire("ข้อความ!", "รหัสผ่านไม่ตรงกันค่ะ");
        } else {
          setStep2(false);
          setStep3(true);
        }
      }
    }
    if (step3) {
      if (
        !bankValue.bankid ||
        !bankValue.accountnumber ||
        !bankValue.accountname
      ) {
        if (!bankValue.accountname) {
          swal.fire("ข้อความ!", "กรุณาเลือกชื่อบัญชีค่ะ");
        }
        if (!bankValue.accountnumber) {
          swal.fire("ข้อความ!", "กรุณากรอกหมายเลขบัญชีค่ะ");
        }
        if (!bankValue.bankid) {
          swal.fire("ข้อความ!", "กรุณาเลือกธนาคารค่ะ");
        }
      } else {
        const body = {
          membertempid: memberTempId,
          password: password1,
          bankid: parseInt(bankValue.bankid),
          accountname: bankValue.accountname,
          accountnumber: bankValue.accountnumber,
          prostatus: bankValue.prostatus,
          uplineid: uplineid,
        };
        await axios({
          url: `${appConfig.REACT_APP_APIURL}${conFirmRegister}`,
          method: "POST",
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
            Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
          },
          data: qs.stringify(body),
        })
          .then((res) => {
            const result = res.data;
            if (result.status === "done") {
              setIsLoading(true);
              setInitialValue(result.msg.result);
              setModalSuccess(true);
              setInitialValue((prev) => ({
                ...prev,
                username: memberPhone,
                password: password1,
              }));
            } else {
              swal.fire(result.msg.msg, result.msg.des);
            }
          })
          .catch((err) => {
            swal.fire("ข้อความ!", "กรุณาลองใหม่อีกครั้ง");
          });
      }
    }
  };

  const [getLogo, setLogo] = useState();
  const [getTitle, setTitle2] = useState();
  const [getBgColor, setBgColor] = useState();
  const [getBgColor2, setBgColor2] = useState();
  const [getBgMainColor, setBgMainColor] = useState();
  const [title, setTitle] = useState();

  useEffect(() => {
    setUpTheme();
    if (bankList.length === 0) {
      setUpBankOption();
    }
  }, []);

  var bgColor0, bgColor1, bgColor2, bgColor, _webname, _title;

  const renderTheme = () => {
    let _storeTheme;
    _webname = localStorage.getItem("webname");
    if (_webname) {
      _storeTheme = localStorage.getItem(`initialTheme${_webname}`);
      if (_storeTheme) {
        _storeTheme = JSON.parse(_storeTheme);
        bgColor0 = _storeTheme.ROOTSTYLE.background.bgColor2;
        bgColor1 = _storeTheme.ROOTSTYLE.navbar.bgColor1;
        bgColor2 = _storeTheme.ROOTSTYLE.navbar.bgColor2;
        bgColor = `linear-gradient(${_storeTheme.COLORMAIN1},${_storeTheme.COLORMAIN2})`;
        setLogo(_storeTheme.LOGO);
        setBgColor(bgColor);
        setBgColor2(bgColor2);
        setBgMainColor(bgColor0);
        _title = _webname ? `BIGWIN - ${_webname}` : `BIGWIN`;
        setTitle(_title);
        document.body.style.background = _storeTheme.COLORBG;
      }
    }
  };

  const setUpTheme = async () => {
    renderTheme();
    await axios({
      url: `${appConfig.REACT_APP_APIURL}${getTheme}`,
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
      },
    }).then((res) => {
      const result = res.data.result;
      bgColor0 = result.ROOTSTYLE.background.bgColor2;
      bgColor1 = result.ROOTSTYLE.navbar.bgColor1;
      bgColor2 = result.ROOTSTYLE.navbar.bgColor2;
      bgColor = `linear-gradient(${result.COLORMAIN1},${result.COLORMAIN2})`;
      //console.log(result);
      let systemName = result.SYSTEMNAME.trim();

      _webname = localStorage.getItem("webname");
      setLogo(result.LOGO);
      setBgColor(bgColor);
      setBgMainColor(bgColor0);
      _title = result.SYSTEMNAME ? `BIGWIN - ${result.SYSTEMNAME}` : `BIGWIN`;
      setTitle(_title);
      if (_webname !== systemName) {
        localStorage.setItem("webname", systemName);
        _webname = systemName;

        renderTheme();
      }
      localStorage.setItem(`initialTheme${_webname}`, JSON.stringify(result));
      document.body.style.background = result.COLORBG;
    });
  };

  const setUpBankOption = async () => {
    await axios({
      url: `${appConfig.REACT_APP_APIURL}${getBankList}`,
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
      },
    }).then((res) => {
      const result = res.data.result;
      setBankList(result);
    });
  };
  const onChangeValue = (value, key) => {
    setBankValue((prev) => ({ ...prev, [key]: value }));
  };

  return (
    <>
      {getLogo && title && (
        <MetaTags>
          <title>{title}</title>
          <meta name="keywords" content={title} />
          <meta name="description" content={title} />
          <meta property="og:title" content={title} />
          <meta property="og:description" content={title} />
          <meta
            property="og:image"
            content={process.env.REACT_APP_IMGURL + "/" + getLogo}
          />
        </MetaTags>
      )}
      <div className="container text-center" style={{ paddingTop: "4.2rem" }}>
        <div
          className="col-md-4 mx-auto"
          style={{
            opacity: isLoading ? 0.4 : "unset",
            pointerEvents: isLoading ? "none" : "unset",
          }}
        >
          <div className="myform" style={{ background: getBgColor }}>
            <div className="logo mb-3">
              <div className="col-md-12 text-center">
                {getLogo && (
                  <img
                    src={process.env.REACT_APP_IMGURL + "/" + getLogo}
                    alt="Bigwin"
                    style={{ maxWidth: "100%", marginBottom: "15px" }}
                  />
                )}
                <h2>สมัครสมาชิก</h2>
              </div>
            </div>

            <form noValidate onSubmit={handleSubmit}>
              {step1 && (
                <>
                  <p className="mt-1 text-white small">
                    กรุณากรอกเบอร์โทรศัพท์ของท่าน
                  </p>
                  <div className="form-group">
                    <InputGroup className="mb-3">
                      <InputGroup.Text id="basic-addon1">
                        <Icon.PersonFill />
                      </InputGroup.Text>
                      <FormControl
                        placeholder="เบอร์โทรศัพท์"
                        type="number"
                        aria-label="memberPhone"
                        aria-describedby="basic-addon1"
                        onChange={(e) => setMemberPhone(e.target.value)}
                      />
                    </InputGroup>
                  </div>
                </>
              )}
              {step2 && (
                <>
                  <p className="mt-1 text-white small">
                    กรุณากรอกรหัสผ่านของท่าน
                  </p>
                  <div className="form-group">
                    <InputGroup className="mb-3">
                      <FormControl
                        placeholder="รหัสผ่าน"
                        type="password"
                        aria-label="password1"
                        aria-describedby="basic-addon1"
                        onChange={(e) => setPassword1(e.target.value)}
                      />
                    </InputGroup>
                    <InputGroup className="mb-3">
                      <FormControl
                        placeholder="ยืนยันรหัสผ่าน"
                        type="password"
                        aria-label="password2"
                        aria-describedby="basic-addon1"
                        onChange={(e) => setPassword2(e.target.value)}
                      />
                    </InputGroup>
                  </div>
                </>
              )}
              {step3 && (
                <>
                  <p className="mt-1 text-white small">
                    กรุณาเลือกธนาคารของท่าน
                  </p>
                  <div className="form-group">
                    <Form.Select
                      className="mb-3"
                      onChange={(e) => onChangeValue(e.target.value, "bankid")}
                      style={{ appearance: "auto" }}
                    >
                      <option value="">เลือกธนาคาร</option>
                      {bankList.length > 0 &&
                        bankList.map((item, index) => {
                          return (
                            <>
                              <option key={index} value={item.bankid}>
                                {item.bankname}
                              </option>
                            </>
                          );
                        })}
                    </Form.Select>
                    <InputGroup className="mb-3">
                      <FormControl
                        placeholder="เลขที่บัญชี"
                        type="number"
                        onChange={(e) =>
                          onChangeValue(e.target.value, "accountnumber")
                        }
                      />
                    </InputGroup>
                    <InputGroup className="">
                      <FormControl
                        placeholder="ชื่อ-นามสกุล"
                        type="text"
                        onChange={(e) =>
                          onChangeValue(e.target.value, "accountname")
                        }
                      />
                    </InputGroup>
                    <div className="row">
                      <label
                        className="l-radio col-5"
                        // style={{ borderTopRightRadius: "none" }}
                      >
                        <Form.Check
                          inline
                          label="รับโบนัส"
                          name="prostatus"
                          type="radio"
                          id="f-option-1"
                          onChange={() => onChangeValue("Y", "prostatus")}
                          checked={bankValue.prostatus === "Y"}
                        />
                      </label>
                      <label className="l-radio col-5">
                        <Form.Check
                          inline
                          label="ไม่รับโบนัส"
                          name="prostatus"
                          type="radio"
                          id="f-option-2"
                          onChange={() => onChangeValue("N", "prostatus")}
                          checked={bankValue.prostatus === "N"}
                        />
                      </label>
                    </div>
                  </div>
                </>
              )}
              <div className="mt-4">
                <button
                  type="submit"
                  className="next action-button btn btn-block mybtn icon btn_login tx-tfm"
                >
                  ต่อไป <Icon.ArrowRight />
                </button>
              </div>
            </form>
            <div className="col-md-12">
              <div className="login-or">
                <hr className="hr-or" />
                <span className="span-or" style={{ background: getBgColor2 }}>
                  หรือ
                </span>
              </div>
            </div>
            <div className="col-md-12 mb-3">
              <p className="text-white">
                มีบัญชีอยู่แล้ว{" "}
                <a
                  style={{
                    color: "#fec33f",
                    textDecoration: "underlin",
                  }}
                  href="../login"
                >
                  เข้าสู่ระบบ
                </a>
              </p>
            </div>
          </div>
        </div>
        {getLogo && (
          <Modal
            show={modalSuccess}
            type={"registerSuccess"}
            initialValue={initialValue}
            logo={process.env.REACT_APP_IMGURL + "/" + getLogo}
            initialTheme={initialTheme}
          />
        )}
        <Spinner
          animation="border"
          variant="warning"
          style={{
            position: "absolute",
            display: isLoading ? "unset" : "none",
            color: "primary.main",
          }}
        />
      </div>
    </>
  );
};
export default Register;
