import React, { useState, useEffect } from "react";
import axios from "axios";
import * as Icon from "react-bootstrap-icons";
import Spinner from "react-bootstrap/Spinner";

import moment from "moment";

import {
  appConfig,
  handleWithdrawAff,
  checkLastLoginReturnVal,
  checkLastLogin,
  logOut,
} from "../../../utils/lib";
import { getWithdrawIncome } from "../../../utils/apiPath";

export const mockData = [
  {
    bonustype: "D",
    status: "C",
    transferinfodate: "2022-03-01 00:01:00",
    transferamount: "1000.00",
  },
];

export const bonusType = {
  D: "ฝาก",
  WL: "แพ้/ชนะ",
  TO: "ค่าคอม",
};
export const statusType = {
  X: "ยกเลิก",
  C: "เรียบร้อย",
  W: "รอดำเนินการ",
};

export const HistoryList = ({ list }) => {
  return (
    <>
      {list.length > 0 ? (
        list.map((item, index) => {
          return (
            <tr key={index}>
              <td className="text-center">
                {moment(item.transferinfodate).format("DD/MM/YYYY HH:mm")}
              </td>
              <td className="text-center">{item.transferamount}</td>
              <td className="text-center">{bonusType[item.bonustype]}</td>
              <td className="text-center">{statusType[item.status]}</td>
            </tr>
          );
        })
      ) : (
        <tr>
          <td colSpan="5" className="text-center">
            ไม่พบข้อมูล
          </td>
        </tr>
      )}
    </>
  );
};

export default function Deposit({
  dateLogin,
  setAffiliateStatus,
  setCashbackStatus,
}) {
  const user = JSON.parse(localStorage.getItem("user"));
  const [isLoading, setIsLoading] = useState(true);
  const [isLoadingWithdraw, setIsLoadingWithdraw] = useState(false);

  const [withdrawIncomeValue, setWithdrawIncomeValue] = useState({
    affiliateInfo: { depositstatus: "", winlossstatus: "", turnoverstatus: "" },
    historyList: [],
  });
  const [offall, setOffall] = useState("");
  const [withdrawMsg, setWithdrawMsg] = useState({ line1: "", line2: "" });

  const onClickAction = async (type) => {
    var stustLogin = await checkLastLoginReturnVal(dateLogin);
    if (stustLogin) {
      handleWithdrawAff(type, setUpWithdrawIncome, setIsLoadingWithdraw);
    } else {
      logOut();
    }
  };

  const setUpWithdrawIncome = async () => {
    let periodText = "";
    let withdrawDate = "";
    const body = { memberid: user.memberid };
    await axios({
      url: `${appConfig.REACT_APP_APIURL}${getWithdrawIncome}`,
      method: "POST",
      timeout: 1000 * 7,
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
      },
      data: body,
    })
      .then((res) => {
        setIsLoading(false);
        let result = res.data.result;
        // result = {
        //   ...result,
        //   historyList: mockData,
        //   affiliateInfo: { activestatus: "N" },
        // };
        // result.affiliateInfo.turnoverstatus = 'Y';

        if (
          (result.affiliateInfo.depositstatus === "N" &&
            result.affiliateInfo.winlossstatus === "N" &&
            result.affiliateInfo.turnoverstatus === "N") ||
          result.affiliateInfo.activestatus === "N"
        ) {
          setOffall("d-none");
        }

        setWithdrawIncomeValue((prev) => ({
          ...prev,
          affiliateInfo: result.affiliateInfo,
          historyList: result.historyList || [],
        }));
        if (result.affiliateInfo.depositpaycircle === "D") {
          periodText = "รายได้แบบรายวัน ";
          withdrawDate = moment().add(1, "d").format("DD/MM/YYYY");
        } else if (result.affiliateInfo.depositpaycircle === "W") {
          periodText = "รายได้แบบรายสัปดาห์ ";
          let wd = moment().day();
          withdrawDate = moment()
            .subtract(wd, "d")
            .add(1, "W")
            .format("DD/MM/YYYY");
        } else if (result.affiliateInfo.depositpaycircle === "M") {
          periodText = "รายได้แบบรายเดือน ";
          withdrawDate = moment().add(1, "M").format("01/MM/YYYY");
        }
        setWithdrawMsg((prev) => ({
          ...prev,
          line1: `* ${periodText}`,
          line2: `* รายได้จะแสดงและถอนได้ ในวันที่ ${withdrawDate} ตั้งแต่เวลา 11:00น. เป็นต้นไปค่ะ`,
        }));
        setAffiliateStatus(result.affiliateInfo.activestatus);
        setCashbackStatus(result.cashbackInfo[5].configvalue);
      })
      .catch((err) => {
        setIsLoading(false);
        console.log("error", err);
        setOffall("d-none");
      });
  };

  useEffect(() => {
    setUpWithdrawIncome();
    checkLastLogin();
  }, []);

  const {
    depositstatus,
    winlossstatus,
    turnoverstatus,
    depositcom_val,
    turnovergamecom_val,
    winlosscom_val,
  } = withdrawIncomeValue.affiliateInfo;

  const { historyList } = withdrawIncomeValue;

  return (
    <>
      {!isLoading && (
        <div
          style={{
            opacity: isLoadingWithdraw ? 0.4 : "unset",
            pointerEvents: isLoadingWithdraw ? "none" : "unset",
          }}
        >
          <h3
            className="text-center mb-3 fade-in"
            style={{ fontWeight: "500" }}
          >
            สถิติ
          </h3>
          <div className="row no-gutters">
            <div className="col-sm-12 col-lg-12 col-12">
              <fieldset className={`form-group text-center mb-0 ${offall}`}>
                <h5 className="text-gold-color" style={{ fontWeight: "500" }}>
                  ถอนรายได้
                </h5>
              </fieldset>

              <fieldset className="form-group">
                <table
                  className={`table table-bordered shadow-sm mb-4 ${offall}`}
                >
                  <thead>
                    <tr>
                      {depositstatus === "Y" && (
                        <th scope="col" className="text-center">
                          ฝาก
                        </th>
                      )}
                      {winlossstatus === "Y" && (
                        <th scope="col" className="text-center">
                          แพ้/ชนะ
                        </th>
                      )}
                      {turnoverstatus === "Y" && (
                        <th scope="col" className="text-center">
                          ค่าคอม
                        </th>
                      )}
                    </tr>
                  </thead>

                  <tbody>
                    <tr>
                      {depositstatus === "Y" && (
                        <td className="text-center val_D">{depositcom_val}</td>
                      )}
                      {winlossstatus === "Y" && (
                        <td className="text-center val_WL">{winlosscom_val}</td>
                      )}
                      {turnoverstatus === "Y" && (
                        <td className="text-center val_TO">
                          {turnovergamecom_val}
                        </td>
                      )}
                    </tr>
                    <tr>
                      {depositstatus === "Y" && (
                        <td className="text-center p-3">
                          <button
                            type="button"
                            className={`btn btn-main btn-sm btn-success btn-rounded btn-afwithdraw ${
                              isLoadingWithdraw ? "disabled" : ""
                            }`}
                            // data-aftype="D"
                            onClick={() => onClickAction("D")}
                          >
                            <Icon.Cash /> ถอน
                          </button>
                        </td>
                      )}
                      {winlossstatus === "Y" && (
                        <td className="text-center p-3">
                          <button
                            type="button"
                            className={`btn btn-main btn-sm btn-success btn-rounded btn-afwithdraw ${
                              isLoadingWithdraw ? "disabled" : ""
                            }`}
                            // data-aftype="WL"
                            onClick={() => onClickAction("WL")}
                          >
                            <Icon.Cash /> ถอน
                          </button>
                        </td>
                      )}
                      {turnoverstatus === "Y" && (
                        <td className="text-center p-3">
                          <button
                            type="button"
                            className={`btn btn-main btn-sm btn-success btn-rounded btn-afwithdraw ${
                              isLoadingWithdraw ? "disabled" : ""
                            }"`}
                            // data-aftype="TO"
                            onClick={() => onClickAction("TO")}
                          >
                            <Icon.Cash /> ถอน
                          </button>
                        </td>
                      )}
                    </tr>

                    <tr>
                      <td colSpan="3">
                        <small style={{ color: "#ccc" }}>
                          {/* * รายได้แบบรายวัน <br />* รายได้จะแสดงและถอนได้
                          ในวันที่ 21/05/2022 ตั้งแต่เวลา 11:00น. เป็นต้นไปค่ะ */}
                          {withdrawMsg.line1}
                          <br />
                          {withdrawMsg.line2}
                        </small>
                      </td>
                    </tr>
                  </tbody>
                </table>
                <fieldset className="form-group text-center  mb-0">
                  <h5 className="text-gold-color" style={{ fontWeight: "500" }}>
                    ประวัติการถอน
                  </h5>
                </fieldset>
                <table className="table table-bordered shadow-sm">
                  <thead>
                    <tr>
                      <th scope="col" className="text-center">
                        วันที่ทำรายการ
                      </th>
                      <th scope="col" className="text-center">
                        จำนวนเงิน
                      </th>
                      <th scope="col" className="text-center">
                        ประเภท
                      </th>
                      <th scope="col" className="text-center">
                        สถานะ
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    <HistoryList list={historyList} />
                  </tbody>
                </table>
              </fieldset>
            </div>
          </div>
        </div>
      )}
      <Spinner
        animation="border"
        variant="warning"
        style={{
          position: "absolute",
          display: isLoading ? "unset" : "none",
          color: "primary.main",
        }}
      />
    </>
  );
}
