/* eslint-disable no-script-url */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect, useState } from "react";
import { redirActivity, logOut, initialTheme, appConfig } from "../../../utils/lib";

export const MenuLeft = ({ setMenuleft, setActiveMenu, handleShow }) => {
  if (!initialTheme) {
    logOut();
  }

  const [getUser, setUser] = useState("");
  const { LINEURL, AISTATUS, HOMEURL, WELCOMEMODAL } = initialTheme || "";
  function MenuLeftHide() {
    document.getElementById("MenuLeft").style.left = "-280px";
    document.getElementById("bgMenuleft").style.display = "none";
    setMenuleft("show");
  }
  useEffect(() => {
    const user = JSON.parse(localStorage.getItem("user"));
    setUser(user.memberusername);
  }, []);

  const changePage = (page) => {
    setActiveMenu(page);
    MenuLeftHide();
  };

  return (
    <>
      <div id="bgMenuleft" onClick={() => MenuLeftHide()}></div>
      <div
        id="MenuLeft"
        className="MenuLeft d-xl-none"
        style={{ background: initialTheme.COLORBG }}
      >
        <div className="x-menu-list-sidebar -open">
          <div className="-inner-wrapper"></div>
          <div className="-brand-heading">
            <a href="/GameList" className="-logo-img text-center px-3">
              <img
                src={window.location.origin + "/" + initialTheme.LOGO}
                alt=""
                className="-logo lazyloaded img-fluid"
              />
            </a>
          </div>
          <div className="-inner-wrapper"></div>

          <div
            className="card-profile mt-2 text-center small badges-profile"
            style={{ color: "#000 !important" }}
          >
            <i className="fa fa-user-circle mr-1"></i>
            <span style={{ color: "#000 !important" }}>{getUser}</span>
          </div>

          <div className="menu_side_bar_info">
            <ul className="navbar-nav menu_sidebar_info mt-3 mb-3">
              <li className="nav-item">
                <a onClick={() => changePage("home")} className="nav-link">
                  <img
                    src={window.location.origin + "/assets/images/ic_home.png?" + appConfig.REACT_APP_IMGVER}
                    alt="menu-icon-slot"
                    className="-img-menu m-auto"
                  />
                  <span className="-text">หน้าแรก</span>
                </a>
              </li>
              <li className="nav-item">
                <button
                  type="button"
                  className="btn-none nav-link"
                  onClick={() => changePage("promotion")}
                >
                  <img
                    src={
                      window.location.origin + "/assets/images/ic_promotion.png?" + appConfig.REACT_APP_IMGVER
                    }
                    alt="menu-icon-slot"
                    className="-img-menu m-auto"
                  />
                  <span className="-text">โปรโมชั่น</span>
                </button>
              </li>
              <li className="nav-item">
                <a
                  onClick={() => window.open(LINEURL ? LINEURL : "", "_blank")}
                  className="nav-link active"
                >
                  <img
                    src={
                      window.location.origin + "/assets/images/ic_contact.png?" + appConfig.REACT_APP_IMGVER
                    }
                    alt="menu-icon-slot"
                    className="-img-menu m-auto"
                  />
                  <span className="-text">ติดต่อเรา</span>
                </a>
              </li>
            </ul>

            <ul className="navbar-nav menu_sidebar_info border_top_menu">
              <li className="nav-item">
                <button
                  type="button"
                  className="btn-none nav-link"
                  onClick={() => changePage("profile")}
                >
                  <img
                    src={
                      window.location.origin + "/assets/images/ic_profile.png?" + appConfig.REACT_APP_IMGVER
                    }
                    alt="menu-icon-slot"
                    className="-img-menu m-auto"
                  />
                  <span className="-text">โปรไฟล์</span>
                </button>
              </li>
              <li className="nav-item">
                <button
                  type="button"
                  className="btn-none nav-link"
                  onClick={() => changePage("recommend")}
                >
                  <img
                    src={
                      window.location.origin + "/assets/images/ic_recommend.png?" + appConfig.REACT_APP_IMGVER
                    }
                    alt="menu-icon-slot"
                    className="-img-menu m-auto"
                  />
                  <span className="-text">รับทรัพย์</span>
                </button>
              </li>
              <li className="nav-item">
                <button
                  type="button"
                  className="btn-none nav-link"
                  onClick={() => changePage("history")}
                >
                  <img
                    src={
                      window.location.origin + "/assets/images/ic_history.png?" + appConfig.REACT_APP_IMGVER
                    }
                    alt="menu-icon-sport"
                    className="-img-menu m-auto"
                  />
                  <span className="-text">ประวัติ</span>
                </button>
              </li>
              {AISTATUS || AISTATUS !== "N" ? (
                <li className="nav-item">
                  <a
                    onClick={() => handleShow("ai")}
                    className="nav-link"
                    target="_blank"
                  >
                    <img
                      src={window.location.origin + "/assets/images/ic_ai.png?" + appConfig.REACT_APP_IMGVER}
                      alt="menu-icon-slot"
                      className="-img-menu m-auto"
                    />
                    <span className="-text">สูตร AI</span>
                  </a>
                </li>
              ) : (
                <></>
              )}
              <li className="nav-item">
                <a
                  onClick={() => redirActivity()}
                  className="nav-link"
                  target="_blank"
                >
                  <img
                    src={
                      window.location.origin + "/assets/images/ic_activity.png?" + appConfig.REACT_APP_IMGVER
                    }
                    alt="menu-icon-slot"
                    className="-img-menu m-auto"
                  />
                  <span className="-text">กิจกรรม</span>
                </a>
              </li>
              {WELCOMEMODAL && (
                <li className="nav-item">
                  <button
                    type="button"
                    className="btn-none nav-item nav-link"
                    onClick={() => handleShow("annou")}
                  >
                    <img
                      src={
                        window.location.origin + "/assets/images/ic_publish.png?" + appConfig.REACT_APP_IMGVER
                      }
                      alt="menu-icon-slot"
                      className="-img-menu m-auto"
                    />
                    <span className="-text">ประกาศ</span>
                  </button>
                </li>
              )}
              <li className="nav-item">
                <a onClick={() => logOut(HOMEURL)} className="nav-link">
                  <img
                    src={
                      window.location.origin + "/assets/images/ic_logout.png?" + appConfig.REACT_APP_IMGVER
                    }
                    alt="menu-icon-sport"
                    className="-img-menu m-auto"
                  />
                  <span className="-text">ออกจากระบบ</span>
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
      {/*
          <div className="-overlay"></div>
        </div> */}
    </>
  );
};
export default MenuLeft;
