import React, { useEffect, useState } from "react";
import moment from "moment";

import Modal from "../../../components/Modal";
import Recommend from "../../../modules/Wallet/Recommend/Index";
import Promotion from "../../../modules/Wallet/Promotion/Promotion";
import Profile from "../../../modules/Wallet/Profile/Profile";
import History from "../../../modules/Wallet/History/History";

import Header from "./Header";
import Home from "../Home/Home";
import Subgame from "../Home/Subgame";
import Footer from "./Footer";

import "../../../utils/MainStyleGameList.css";

import {
  fecthGetTheme,
  setUpProFile,
  fecthGetBalance,
  initialTheme,
  checkLastLogin
} from "../../../utils/lib";

import Spinner from "react-bootstrap/Spinner";

export const Layout = () => {
  const dateLogin = localStorage.getItem("dateLogin")
    ? localStorage.getItem("dateLogin")
    : "";
  const [isLoading, setIsLoading] = useState(true);
  const [show, setShow] = useState(false);
  const [modalType, setModalType] = useState("");
  const [activeMenu, setActiveMenu] = useState("home");
  const [Maingamename, setMaingamename] = useState("");
  const [subgameId, setSubgameId] = useState("");
  const [credit, setCredit] = useState(0.0);
  const [initialValueProfile, setInitialValueProfile] = useState({
    accountname: "",
    accountnumber: "",
    bankcode: "",
    bankname: "",
    createdate: "",
    lineid: "",
    logindate: "",
    memberusername: "",
    prostatus: "",
  });
  const [isTimeDelay, setIsTimeDelay] = useState(moment(new Date()).format("HH:mm:ss"));
  const user = JSON.parse(localStorage.getItem("user"));
  const [isPage, setIsPage] = useState(true);

  useEffect(() => {
    fecthGetTheme();
    setUpProFile(setInitialValueProfile);
    setIsLoading(false);
    setTimeout(() => {
      recheckCredit();
      setIsLoading(true);
    }, user.welcomemodal ? 0 : 2000);
  }, []);

  const handleClose = () => setShow(false);

  const handleShow = (modalType) => {
    setShow(true);
    setModalType(modalType);
  };

  const recheckCredit = async (e) => {
    if (e) {
      setIsLoading(false);
      checkLastLogin();
    }
    if (isTimeDelay <= moment(new Date()).format("HH:mm:ss")) {
      var getCreadit = await fecthGetBalance();
      setCredit(getCreadit.value);
      setIsLoading(getCreadit.status);
      setIsTimeDelay(moment(new Date()).add(5, 'seconds').format("HH:mm:ss"));
    } else {
      setTimeout(() => { setIsLoading(true); }, 350);
    }
  };

  return (
    <>
      <Header
        activeMenu={activeMenu}
        setActiveMenu={setActiveMenu}
        handleShow={handleShow}
        recheckCredit={recheckCredit}
        initialTheme={initialTheme}
        credit={credit}
      />
      <div
        style={{
          opacity: isLoading ? "unset" : 0.4,
          pointerEvents: isLoading ? "unset" : "none",
        }}
      >
        {activeMenu === "home" && (
          <Home
            dateLogin={dateLogin}
            setActiveMenu={setActiveMenu}
            initialTheme={initialTheme}
            setSubgameId={setSubgameId}
            setMaingamename={setMaingamename}
            setIsLoading={setIsLoading}
          />
        )}

        {activeMenu === "subgame" && (
          <Subgame
            dateLogin={dateLogin}
            setActiveMenu={setActiveMenu}
            subgameId={subgameId}
            setIsLoading={setIsLoading}
            Maingamename={Maingamename}
            setIsPage={setIsPage}
          />
        )}
        <div className="gamelist-layout">
          {activeMenu === "recommend" && (
            <Recommend
              initialTheme={initialTheme}
              defaultMenu={"recommend"}
              setIsPage={setIsPage}
            />
          )}
          {activeMenu === "cashback" && (
            <Recommend
              initialTheme={initialTheme}
              defaultMenu={"cashback"}
              setIsPage={setIsPage}
            />
          )}
          {activeMenu === "promotion" && <Promotion setIsPage={setIsPage} />}
          {activeMenu === "profile" && (
            <Profile
              initialValue={initialValueProfile}
              setInitialValue={setInitialValueProfile}
              initialTheme={initialTheme}
              setIsPage={setIsPage}
            />
          )}
          {activeMenu === "history" && <History setIsPage={setIsPage} />}
        </div>
      </div>
      <Footer
        setActiveMenu={setActiveMenu}
        handleShow={handleShow}
        initialTheme={initialTheme}
      />
      <Modal
        setActiveMenu={setActiveMenu}
        show={show}
        setShow={setShow}
        handleClose={handleClose}
        type={modalType}
        initialValue={initialValueProfile}
        setInitialValue={setInitialValueProfile}
        credit={credit}
        setCredit={setCredit}
        initialTheme={initialTheme}
      />
      <Spinner
        animation="border"
        variant="warning"
        style={{
          position: "absolute",
          display: isLoading ? "none" : "unset",
          color: "primary.main",
        }}
      />
    </>
  );
};

export default Layout;
