import React, { useEffect, useState } from "react";
import * as Icon from "react-bootstrap-icons";
import {
  fecthGetBalance,
  handleWithdraw,
  checkLastLogin,
  checkLastLoginReturnVal,
  logOut,
} from "../../../utils/lib";

export const Withdraw = ({
  initialValue,
  credit,
  setCredit,
  show,
  setShow,
  dateLogin,
}) => {
  const [buttonLoding, setButtonLoding] = useState(true);

  const recheckCredit = async () => {
    var getCreadit = await fecthGetBalance();
    if (getCreadit.status) {
      setCredit(getCreadit.value);
    }
  };

  const onClickAction = async () => {
    var stustLogin = await checkLastLoginReturnVal(dateLogin);
    if (stustLogin) {
      handleWithdraw(setButtonLoding, setShow, setCredit);
    } else {
      logOut();
    }
  };

  if (!show) {
    recheckCredit();
  }

  useEffect(() => {
    checkLastLogin();
  }, []);

  return (
    <>
      <div className="modal-body">
        <div className="row">
          <div className="col-12">
            <div className="card-bank mt-3 mb-3">
              <div className="row">
                <div className="col">
                  <h4 className="text-left">ยอดเงินทั้งหมด</h4>
                  <hr style={{ borderColor: "#ffc107" }} />
                  <h4 className="text-left text-highlight">
                    <span id="credits_withdraw">{credit}</span> บาท
                  </h4>
                </div>
              </div>
            </div>
            <div className="card-bank">
              <div className="row">
                <div className="col-auto">
                  <figure className="avatar avatar-50 active">
                    <img
                      src={
                        window.location.origin +
                        "/assets/images/icon_bank/" +
                        initialValue.bankcode.toLowerCase() +
                        ".jpg"
                      }
                      title="กสิกรไทย"
                      alt=""
                    />
                  </figure>
                </div>
                <div className="col pl-0 align-self-center">
                  <p className="mb-0  d-flex">
                    {" "}
                    เลขที่บัญชี: {initialValue.accountnumber}
                  </p>
                  <p className="mb-0 ">ธนาคาร: {initialValue.bankname}</p>
                  <p className="mb-0 text-mute ">
                    {" "}
                    ชื่อ: {initialValue.accountname}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div
        className="modal-footer"
        style={{ borderTop: "none", marginBottom: "20px" }}
      >
        <button
          id="todo_withdraw"
          type="button"
          className={`btn btn-success btn-rounded btn-block ${
            buttonLoding ? "" : "disabled"
          }`}
          onClick={() => onClickAction()}
        >
          <i className="fa fa-money" aria-hidden="true"></i>{" "}
          <span className="txt_withdraw">
            <Icon.Cash /> {buttonLoding ? "ถอนเงิน" : "กรุณารอสักครู่..."}
          </span>{" "}
        </button>
      </div>
    </>
  );
};
export default Withdraw;
