import React, { useState, useEffect } from "react";
import axios from "axios";
import Spinner from "react-bootstrap/Spinner";

import {
  appConfig,
  handleWithdrawCashBack,
  checkLastLogin,
  checkLastLoginReturnVal,
  logOut,
} from "../../../utils/lib";
import { geCashback } from "../../../utils/apiPath";

export const mockData = [
  {
    period: "2022-07-01",
    winloss: "-222.00",
    winlosscom: "19.99",
  },
];

export const HistoryList = ({ list }) => {
  return (
    <>
      {list.length > 0 ? (
        list.map((item, index) => {
          return (
            <tr key={index}>
              <td className="text-center">{item.period}</td>
              <td
                className={`text-center ${
                  parseInt(item.winloss, 10) >= 0
                    ? "text-success"
                    : "text-danger"
                }`}
              >
                {item.winloss}
              </td>
              <td
                className={`text-center ${
                  parseInt(item.winlosscom, 10) >= 0
                    ? "text-success"
                    : "text-danger"
                }`}
              >
                {item.winlosscom}
              </td>
            </tr>
          );
        })
      ) : (
        <tr>
          <td colSpan="5" className="text-center">
            ไม่พบข้อมูล
          </td>
        </tr>
      )}
    </>
  );
};

export default function Cashback({
  dateLogin,
  setAffiliateStatus,
  setCashbackStatus,
}) {
  const user = JSON.parse(localStorage.getItem("user"));
  const token = localStorage.getItem("accessToken");

  const [isLoading, setIsLoading] = useState(true);
  const [isLoadingWithdraw, setIsLoadingWithdraw] = useState(false);

  const [cashbackValue, setCashbackValue] = useState({});
  const [periodText, setPeriodText] = useState("");

  const onClickAction = async () => {
    var stustLogin = await checkLastLoginReturnVal(dateLogin);
    if (stustLogin) {
      handleWithdrawCashBack(setUpCashback, setIsLoadingWithdraw);
    } else {
      logOut();
    }
  };

  const setUpCashback = async () => {
    let cashBackCon = "";
    const body = { memberid: user.memberid };
    await axios({
      url: `${appConfig.REACT_APP_APIURL}${geCashback}`,
      method: "POST",
      timeout: 1000 * 7,
      headers: {
        "Content-Type": "application/json",
        "x-access-token": token,
        Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
      },
      data: body,
    })
      .then((res) => {
        setIsLoading(false);
        let result = res.data.result;
        // result = {
        //   ...result,
        //   winlossDatas: { datas: mockData },
        // };
        cashBackCon = result.cashBackInfo.CASHBACK_CONDITION;
        if (cashBackCon === "D") {
          setPeriodText("ในวันที่ผ่านมา ");
        } else if (cashBackCon === "W") {
          setPeriodText("ในสัปดาห์ที่ผ่านมา ");
        } else if (cashBackCon === "M") {
          setPeriodText("ในเดือนที่ผ่านมา ");
        }
        // setAffiliateStatus(result.affiliateInfo.activestatus);
        setCashbackStatus(result.cashBackInfo.CASHBACK_USESTATUS);
        setCashbackValue(result);
      })
      .catch((err) => {
        setIsLoading(false);
        console.log("error", err);
      });
  };

  const { cashbackAmount } = cashbackValue || "";

  const { CASHBACK_PERCENT, CASHBACK_MAX, CASHBACK_MIN } =
    cashbackValue?.cashBackInfo || "";

  const { datas } = cashbackValue?.winlossDatas || "";

  useEffect(() => {
    setUpCashback();
    checkLastLogin();
  }, []);

  return (
    <>
      {!isLoading && (
        <div
          style={{
            opacity: isLoadingWithdraw ? 0.4 : "unset",
            pointerEvents: isLoadingWithdraw ? "none" : "unset",
          }}
        >
          <h3
            className="text-center mb-3 fade-in"
            style={{ fontWeight: "500" }}
          >
            คืนยอดเสีย
          </h3>
          <div className="row no-gutters">
            <div className="col-sm-12 col-lg-12 col-12">
              <fieldset className="form-group">
                <table className="table table-bordered shadow-sm mb-4 ">
                  <thead>
                    <tr>
                      <th scope="col" className="text-center">
                        <p style={{ textAlign: "center", fontWeight: "500" }}>
                          {CASHBACK_PERCENT}% ของยอดเสีย{periodText}
                        </p>
                        <p style={{ textAlign: "center", fontWeight: "500" }}>
                          ยอดจ่ายสูงสุด {Number(CASHBACK_MAX).toFixed(2)} บาท
                        </p>
                      </th>
                    </tr>
                  </thead>

                  <tbody>
                    <tr>
                      <td className="text-center val_D">
                        {Number(cashbackAmount).toFixed(2)}
                      </td>
                    </tr>

                    <tr>
                      <td className="text-center p-3">
                        {cashbackAmount >= CASHBACK_MIN ? (
                          <button
                            type="button"
                            className="btn btn-main btn-sm btn-success btn-rounded"
                            onClick={() => onClickAction()}
                          >
                            <i className="fa fa-money fa-sm mr-1"></i>ถอน
                          </button>
                        ) : (
                          <button
                            type="button"
                            className="btn btn-main btn-sm btn-secondary btn-rounded"
                          >
                            <i className="fa fa-money fa-sm mr-1"></i>ถอน
                          </button>
                        )}
                      </td>
                    </tr>
                    <tr className="text-center">
                      <td>
                        <p style={{ color: "red", fontWeight: "500" }}>
                          เมื่อมียอดเสียมากกว่า{" "}
                          {Number(CASHBACK_MIN).toFixed(2)} บาท ขึ้นไป
                        </p>
                      </td>
                    </tr>
                  </tbody>
                </table>
                <fieldset className="form-group text-center  mb-0">
                  <h5 className="text-gold-color" style={{ fontWeight: "500" }}>
                    ประวัติแพ้ชนะ
                  </h5>
                </fieldset>
                <table className="table table-bordered shadow-sm">
                  <thead>
                    <tr>
                      <th scope="col" className="text-center">
                        วันที่
                      </th>
                      <th scope="col" className="text-center">
                        แพ้ชนะ
                      </th>
                      <th scope="col" className="text-center">
                        ยอดได้คืน
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    <HistoryList list={datas} />
                  </tbody>
                </table>

                <fieldset className="form-group text-center  mb-0">
                  <p className="text-gold-color" style={{ fontWeight: "500" }}>
                    ยอด <span className="text-success">+</span>
                    และมี <span className="text-success">สีเขียว</span> คือ
                    เว็บแพ้ ส่วนผู้เล่นชนะ
                  </p>
                  <p className="text-gold-color" style={{ fontWeight: "500" }}>
                    ยอด <span className="text-danger">-</span>
                    และมี <span className="text-danger">สีแดง</span> คือ เว็บชนะ
                    ส่วนผู้เล่นแพ้
                  </p>
                  <p
                    className="text-gold-color"
                    style={{ fontWeight: "500", color: "#fec33f" }}
                  >
                    ผู้เล่นที่มีสิทธิได้รับการคืนยอดเสีย
                  </p>
                  <p
                    className="text-gold-color"
                    style={{ fontWeight: "500", color: "#fec33f" }}
                  >
                    ยอดได้คืน จะต้องเป็น + แล้วมีสีเขียว
                  </p>
                  <p
                    className="text-gold-color"
                    style={{ fontWeight: "500", color: "#fec33f" }}
                  >
                    และยอดรวมในวันที่ผ่านมา จะต้องมากกว่า{" "}
                    {Number(CASHBACK_MIN).toFixed(2)} บาท
                  </p>

                  <p className="text-gold-color" style={{ fontWeight: "500" }}>
                    *** เงื่อนไขในการรับ ***
                  </p>

                  <p className="text-gold-color" style={{ fontWeight: "500" }}>
                    &nbsp; &nbsp; - ลูกค้าจะต้องไม่กดรับ โบนัส ทุกประเภท
                  </p>
                  <p className="text-gold-color" style={{ fontWeight: "500" }}>
                    &nbsp; &nbsp; - การรับโบนัสจากการเติมโดยแอดมิน
                  </p>
                  <p className="text-gold-color" style={{ fontWeight: "500" }}>
                    &nbsp; &nbsp; - โบนัสจากกิจกรรมทุกประเภท
                  </p>
                </fieldset>
              </fieldset>
            </div>
          </div>
        </div>
      )}
      <Spinner
        animation="border"
        variant="warning"
        style={{
          position: "absolute",
          display: isLoading ? "unset" : "none",
          color: "primary.main",
        }}
      />
    </>
  );
}
