/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState, useEffect } from "react";

import { getApiBaccarat } from "../../../utils/apiPath";
import { appConfig, checkLastLogin } from "../../../utils/lib";
import { Base64 } from "js-base64";

import Swal from "sweetalert2";
import axios from "axios";
import moment from "moment";

export const AI = ({ initialTheme, initialValue, dateLogin }) => {
  const user = JSON.parse(localStorage.getItem("user"));
  // const token = localStorage.getItem("accessToken");

  const { AISTATUS } = initialTheme;
  const { tutorial } = initialValue;

  const vax = AISTATUS.split(".");

  useEffect(() => {
    checkLastLogin();
  }, []);

  const baccaratLink = async () => {
    const body = { memberusername: user.memberusername };
    await axios({
      url: `${appConfig.REACT_APP_APIURL}${getApiBaccarat}`,
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
      },
      data: body,
    }).then((res) => {
      // setIsLoading(true);
      if (res.data.datas.status === 1) {
        const urlgame = res.data.datas.url;
        setTimeout(() => {
          window.open(urlgame, "_blank");
        });
      } else {
        Swal.fire({
          text: res.data.datas.message,
          icon: "error",
          confirmButtonText: "Close",
        });
      }
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const userToken = Base64.encode(
      user.memberusername +
      "|" +
      moment().format("YYYY-MM-DD HH:mm:s") +
      "|" +
      tutorial
    );
    const token = userToken.replaceAll("=", "");
    const body = { memberid: user.memberid, token: token };

    // console.log(body);
    window.open(AISTATUS + "?t=" + token, "_blank");
  };

  return (
    <div className="modal-body">
      <div className="row">
        <div className="col-6">
          <a
            onClick={() => baccaratLink()}
            id="btn-ai-formx"
            className="menu_txt "
            target="_blank"
          >
            <img
              src={`${window.location.origin}/assets/images/icon_bar.png?${appConfig.REACT_APP_IMGVER}`}
              style={{ maxHeight: "130px", margin: "auto", display: "block" }}
            />
            <p
              className="text-center"
              style={{ marginTop: "10px", color: "#FFF" }}
            >
              Baccarat Lock
            </p>
          </a>
        </div>
        <div className="col-6">
          {vax && vax[1] === "boost-bonus" ? (
            <form noValidate onSubmit={handleSubmit}>
              <a
                onClick={handleSubmit}
                id="btn-ai-bonus-form"
                className="menu_txt btn-ai-bonus-form"
                onclick=""
              >
                <img
                  src={`${window.location.origin}/assets/images/logo-boost-bonus.png?${appConfig.REACT_APP_IMGVER}`}
                  style={{
                    maxHeight: "130px",
                    margin: "auto",
                    display: "block",
                  }}
                />
                <p
                  className="text-center"
                  style={{ marginTop: "10px", color: "#FFF" }}
                >
                  Boost Bonus
                </p>
              </a>
            </form>
          ) : (
            <form noValidate onSubmit={handleSubmit}>
              <a
                onClick={handleSubmit}
                id="btn-ai-bonus-form"
                className="menu_txt btn-ai-bonus-form"
              >
                <img
                  src={window.location.origin + "/assets/images/icon_javit.png"}
                  style={{ maxHeight: "130px", margin: "auto", display: "block" }}
                />
                <p
                  className="text-center"
                  style={{ marginTop: "10px", color: "#FFF" }}
                >
                  Javis
                </p>
              </a>
            </form>
          )}
        </div>
      </div>
    </div>
  );
};
export default AI;
