export const checkLoginExpire = (dateLogin) => {
  const _now = new Date();
  const _dateLogin = new Date(dateLogin);
  const hours = parseInt((Math.abs(_dateLogin - _now) / (1000 * 60 * 60)) % 24);
  // const minutes = parseInt(
  //   (Math.abs(_dateLogin.getTime() - _now.getTime()) / (1000 * 60)) % 60
  // );
  if (hours >= 46) {
    removeLocalStorage();
    window.location.href = "/";
  }
};

export const removeLocalStorage = () => {
  localStorage.clear();
};

export const logOut = (HOMEURL = "") => {
  removeLocalStorage();
  let redir = HOMEURL ? HOMEURL : "/";
  window.location.href = redir;
};

export const checkResponse = (response) => {
  if (response.request.status === 429 || response.request.status === 401) {
    logOut();
  }
};

export const fecthGetBalance = async () => {
  const user = JSON.parse(localStorage.getItem("user"));

  const body = {};
  let result = {
    status: false,
    value: [],
    code: "",
  };
  const headers = {
    "Content-Type": "application/json",
    "x-access-token": token,
    Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
  };
  try {
    const resultValue = await axios.post(
      appConfig.REACT_APP_APIURL + "" + getBalance,
      body,
      {
        headers,
      }
    );
    result = {
      ...result,
      status: true,
      value:
        typeof resultValue.data.result.result.credit === "number"
          ? resultValue.data.result.result.credit.toFixed(2)
          : 0,
      code: resultValue.data.result.code ? resultValue.data.result.code : 0,
    };
  } catch (err) {
    if (parseInt(err.response.status) === 429) {
      logOut();
    }
    var errorCredit = 0;
    result = {
      ...result,
      status: true,
      value: user?.lastCredit || errorCredit.toFixed(2),
    };
    // checkErrorApi(err.response.status);
  }
  // console.log("result", result);

  return result;
};

export const fecthGetTheme = async () => {
  await axios({
    url: `${appConfig.REACT_APP_APIURL}${getTheme}`,
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
    },
  }).then((res) => {
    const result = res.data.result;
    let systemName = result.SYSTEMNAME.trim();

    _webname = localStorage.getItem("webname");
    if (_webname !== systemName) {
      localStorage.setItem("webname", systemName);
      _webname = systemName;
    }
    localStorage.setItem(`initialTheme${_webname}`, JSON.stringify(result));
  });
};

export const validatePhoneNumber = (inputtxt) => {
  let result = {
    status: false,
    msg: "",
  };

  if (inputtxt.length !== 10) {
    result = {
      ...result,
      msg: "ข้อมูล เบอร์โทร จำนวนตัวอักษรต้อง 10 ตัวเท่านั้น",
    };
  } else {
    var phoneno = /^\d{10}$/;
    if (inputtxt.match(phoneno)) {
      result = {
        ...result,
        status: true,
      };
    } else {
      result = {
        ...result,
        msg: "กรุณากรอกเบอร์โทรศัพท์เป็นตัวเลขทั้งหมด",
      };
    }
  }

  return result;
};

