export const authenLogin = async (value, setIsLoading) => {
  const body = {
    username: value.username,
    password: value.password,
  };
  await axios({
    url: `${appConfig.REACT_APP_APIURL}${loginAuth}`,
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
    },
    data: body,
  }).then((res) => {
    const result = {
      ...res.data,
      welcomemodal: true,
    };
    if ("accessToken" in result) {
      setIsLoading(true);
      localStorage.setItem("accessToken", result["accessToken"]);
      localStorage.setItem("user", JSON.stringify(result));
      localStorage.setItem("tour", JSON.stringify(true));

      // if (theme !== "2") {
      //   window.location.href = "/";
      // } else {
      //   window.location.href = "/Gamelist";
      // }
    } else {
      swal.fire("ข้อความ!", result.msg);
    }
  });
};

