export const redirActivity = async () => {
  const body = {
    memberid: user.memberid,
    memberusername: user.memberusername,
  };
  await axios({
    url: `${appConfig.REACT_APP_APIURL}${getActivity}`,
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
    },
    data: body,
  }).then((res) => {
    const result = res.data;
    if (result.status === "done") {
      const urlActivity = result.result;
      setTimeout(() => {
        window.open(urlActivity, "_blank");
      });
    }
  });
};

export const updateStatusPro = async (
  status,
  setIsLoading,
  setInitialValue
) => {
  setIsLoading(true);
  const body = { prostatus: status };
  await axios({
    url: `${appConfig.REACT_APP_APIURL}${updateProfile}`,
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      "x-access-token": token,
      Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
    },
    data: body,
  })
    .then((res) => {
      const result = res.data.status;
      if (result === "done") {
        setIsLoading(false);
        setInitialValue((prev) => ({ ...prev, prostatus: status }));
      }
    })
    .catch((err) => {
      setIsLoading(false);
      swal.fire({
        icon: "error",
        title: "failed!",
        text: err.response.data.msg,
      });
    });
};

