export const onClickCopy = (value, type, hidenValue) => {
  const text = {
    accountnumber: {
      title: "คัดลอกเลขบัญชีเรียบร้อยค่ะ",
      text: "กรุณาโอนไปยังบัญชีเพื่อเติมเงิน",
    },
    link: {
      title: "คัดลอก Link เรียบร้อยค่ะ",
      text: "กรุณาส่ง Link ไปยังเพื่อนคุณ",
    },
  };
  if (type === "accountnumber") {
    const findValueCorrect = !!hidenValue.find((item, index) => {
      return value === item.accountnumber.replaceAll("-", "");
    });
    if (findValueCorrect) {
      var textField = document.createElement("textarea");
      textField.innerText = value;
      document.body.appendChild(textField);
      textField.select();
      document.execCommand("copy");
      textField.remove();
    } else {
      swal.fire({
        icon: "error",
        title: "เกิดข้อผิดพลาด",
        text: "เกิดข้อผิดพลาด กรุณาลองใหม่",
        timer: 1500,
      });
      return false;
    }
  } else {
    var textField = document.createElement("textarea");
    textField.innerText = value;
    document.body.appendChild(textField);
    textField.select();
    document.execCommand("copy");
    textField.remove();
  }

  // navigator.clipboard.writeText(value);

  swal.fire({
    icon: "success",
    title: text[type].title,
    text: text[type].text,
    timer: 1500,
  });
};

export const convertPeriod = (period) => {
  let y = period.substring(0, 4);
  let m = period.substring(4, 6);
  let d = period.substring(6, 8);

  const format = d + "/" + m + "/" + y;
  return format;
};

export const findArrConfig = (arrayArrConfig, value) => {
  let findValue = "";
  if (arrayArrConfig && arrayArrConfig.length > 0) {
    findValue = arrayArrConfig.find((item) => item.configname === value);
  }
  return findValue?.configvalue || "";
};

