export const checkFirstLogin = async (dateLoginNow) => {
  const getToken = localStorage.getItem("accessToken");
  if (!getToken) {
    await logOut();
    return false;
  }

  if (dateLoginNow) {
    const body = {};
    await axios({
      url: `${appConfig.REACT_APP_APIURL}${lastLogin}`,
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "x-access-token": token,
        Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
      },
      data: body,
    })
      .then((res) => {
        const result = res.data.result;
        if (result.logindate !== dateLoginNow) {
          logOut();
        }
      })
      .catch((err) => {
        checkErrorApi(err.response.status);
      });
  }
};
export const checkTimeBuild = async (dateLoginNow) => {
  const dateBuildTimeStamp = moment(
    preval`module.exports = new Date();`
  ).format("DD/MM/YYYY HH:mm:ss");
  const dateLoginFormat = moment(dateLoginNow).format("DD/MM/YYYY HH:mm:ss");

  if (dateLoginFormat < dateBuildTimeStamp) {
    await logOut();
    return false;
  }
};
export const checkLastLogin = async (ignore = false) => {
  const getToken = localStorage.getItem("accessToken");
  const getDateLogin = localStorage.getItem("dateLogin");
  var _webname = localStorage.getItem("webname");

  const getInitialTheme = localStorage.getItem(`initialTheme${_webname}`);

  if (!getToken || !getDateLogin || !getInitialTheme) {
    await logOut();
    return false;
  }
  if (getDateLogin) {
    // checkTimeBuild(dateLoginNow);

    const body = {};
    await axios({
      url: `${appConfig.REACT_APP_APIURL}${lastLogin}`,
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "x-access-token": token,
        Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
      },
      data: body,
    })
      .then((res) => {
        const result = res.data.result;
        if (result.logindate !== getDateLogin) {
          logOut();
        }
      })
      .catch((err) => {
        if (!ignore) {
          checkErrorApi(err.response.status);
        } else {
          if (err.response.status === 500 && 501 && 502) {
            checkErrorApi(err.response.status);
          }
        }
      });
  }
};

export const checkLastLoginReturnVal = async (dateLoginNow) => {
  const getToken = localStorage.getItem("accessToken");
  const getdateLogin = localStorage.getItem("dateLogin");
  var _webname = localStorage.getItem("webname");
  const getInitialTheme = localStorage.getItem(`initialTheme${_webname}`);

  if (!getToken || !getdateLogin || !getInitialTheme) {
    return false;
  }

  const body = {};
  let result = true;
  const headers = {
    "Content-Type": "application/json",
    "x-access-token": token,
    Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
  };

  if (getdateLogin) {
    try {
      const resultValue = await axios.post(
        appConfig.REACT_APP_APIURL + "" + lastLogin,
        body,
        {
          headers,
        }
      );
      if (resultValue.data.result.logindate !== getdateLogin) {
        result = false;
      }
    } catch (err) {
      result = false;
      console.log("fecthGetBalance : error => ", err.response);
      checkErrorApi(err.response.status);
    }
  }

  return result;
};

export const checkErrorApi = async (status) => {
  if (status) {
    if (parseInt(status) === 429) {
      await logOut();
    } else {
      swal.fire({
        icon: "error",
        title: "เกิดข้อผิดพลาด",
        text: `เกิดข้อผิดพลาด Code ${status} กรุณาลองใหม่อีกครั้ง!`,
      });
      setTimeout(async () => {
        await logOut();
      }, 2000);
    }
  }
  // console.log('status',status);
};
