/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState, useEffect } from "react";
import { subGameList } from "../../../utils/apiPath";
import {
  chkMobile,
  appConfig,
  checkLastLogin,
  checkLastLoginReturnVal,
  logOut,
} from "../../../utils/lib";
import { v4 as uuidv4 } from "uuid";
import Swal from "sweetalert2";
import axios from "axios";

export const Subgame = ({
  dateLogin,
  setActiveMenu,
  subgameId,
  setIsLoading,
  Maingamename,
}) => {
  const [getGameList, setgetGameList] = useState({
    gamename: "",
    imageurl: "",
    active_status: "",
  });
  const [device, setDevice] = useState("");

  const memberid = JSON.parse(localStorage.getItem("user"));
  const gameListSub = async () => {
    await axios({
      url: `${appConfig.REACT_APP_APIURL}${subGameList}/${subgameId}`,
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
      },
      data: { memberid: memberid.memberid },
    }).then((res) => {
      // console.log(res);
      const result = res.data.result.rows;
      setgetGameList(result);
    });
  };

  const reGame = async (link) => {
    setIsLoading(false);
    // checkLastLogin();
    var stustLogin = await checkLastLoginReturnVal(dateLogin);
    if (stustLogin) {
      await axios({
        url: `${appConfig.REACT_APP_APIURL}${link}${device}`,
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
        },
      }).then((res) => {
        setIsLoading(true);
        if (res.data.datas.code === 0) {
          const urlgame = res.data.datas.url;
          setTimeout(() => {
            window.open(urlgame, "_blank");
          });
        } else {
          Swal.fire({
            text: res.data.datas.message,
            icon: "error",
            confirmButtonText: "Close",
          });
        }
      });
    } else {
      logOut();
    }
  };

  useEffect(() => {
    setDevice(chkMobile);
    gameListSub();
    checkLastLogin();
  }, []);

  return (
    <div className="container subGame">
      {/* btn-blu-grow */}
      <button
        type="button"
        className="btn btn-sm btn-golder mt-2"
        onClick={() => setActiveMenu("home")}
      >
        กลับ
      </button>
      <span className="float-end mt-2 gamename_sub">{Maingamename}</span>
      <div className="row">
        {getGameList.length > 0 &&
          getGameList.map((item, index) => {
            return (
              <>
                <div
                  key={uuidv4()}
                  className="col-3 col-sm-3 col-md-2 col-lg-2 mt-3"
                >
                  <div onClick={() => reGame(item.link)} className="pointer">
                    <img
                      src={item.imageurl}
                      className="w-100 subGameimage"
                      alt={item.gamename}
                    />
                    <p className="subGametext">{item.gamename}</p>
                  </div>
                </div>
              </>
            );
          })}
      </div>
    </div>
  );
};

export default Subgame;
