import React, { useState, useEffect } from "react";
import { chkMobile } from "../../utils/lib";

const Pagination = ({ dataPerPage, totalData, currentPage, paginate }) => {
  const [device, setDevice] = useState("");

  const pageNumbers = [];

  for (let i = 1; i <= Math.ceil(totalData / dataPerPage); i++) {
    pageNumbers.push(i);
  }

  useEffect(() => {
    setDevice(chkMobile);
  }, []);

  return (
    <nav>
      <ul className="pagination">
        {pageNumbers.length > 0 && (
          <>
            <li className="page-item">
              <a
                onClick={() => paginate(1)}
                className="page-link"
                style={{ width: "4.5rem" }}
              >
                หน้าแรก
              </a>
            </li>
            <li className="page-item">
              {currentPage > 1 ? (
                <a
                  onClick={() => paginate(currentPage - 1)}
                  className="page-link"
                >
                  {"<"}
                </a>
              ) : (
                <a className="page-link">{"<"}</a>
              )}
            </li>
          </>
        )}

        {currentPage <= (device === "d" ? 5 : 2)
          ? pageNumbers.slice(0, (device === "d" ? 5 : 2) + 1).map((number) => (
              <li key={number} className="page-item">
                <a
                  onClick={() => paginate(number)}
                  className="page-link"
                  style={currentPage === number ? { color: "#f9fafb" } : {}}
                >
                  {number}
                </a>
              </li>
            ))
          : pageNumbers
              .slice(currentPage - (device === "d" ? 5 : 2), currentPage + 1)
              .map((number) => (
                <li key={number} className="page-item">
                  <a
                    onClick={() => paginate(number)}
                    className="page-link"
                    style={currentPage === number ? { color: "#f9fafb" } : {}}
                  >
                    {number}
                  </a>
                </li>
              ))}
        {pageNumbers.length > 0 && (
          <>
            <li className="page-item">
              {currentPage < pageNumbers.length ? (
                <a
                  onClick={() => paginate(currentPage + 1)}
                  className="page-link"
                >
                  {">"}
                </a>
              ) : (
                <a className="page-link">{">"}</a>
              )}
            </li>
            <li className="page-item">
              <a
                onClick={() => paginate(pageNumbers.length)}
                className="page-link"
                style={{ width: "5.5rem" }}
              >
                หน้าสุดท้าย
              </a>
            </li>
          </>
        )}
      </ul>
    </nav>
  );
};

export default Pagination;
