/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable jsx-a11y/img-redundant-alt */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState, useEffect, useRef } from "react";

import Navbar from "react-bootstrap/Navbar";
import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import { ArrowRepeat } from "react-bootstrap-icons";
import { GearFill } from "react-bootstrap-icons";
import { logOut } from "../../../utils/lib";
import { redirActivity, initialTheme } from "../../../utils/lib";
import MenuLeft from "../../../components/Modal/Content/MenuLeft";

export const Header = ({
  activeMenu,
  setActiveMenu,
  handleShow,
  recheckCredit,
  credit,
}) => {
  const [isShow, setIsShow] = useState(false);
  const handleDropDown = () => setIsShow(!isShow);
  const [getLogo, setLogo] = useState();
  const [getBgColor, setBgColor] = useState();
  const [getMenuleft, setMenuleft] = useState("show");
  const user = JSON.parse(localStorage.getItem("user"));
  const { THEME, HOMEURL } = initialTheme || "";

  useEffect(() => {
    const bgColor0 = initialTheme.COLORBG;
    document.body.style.background = bgColor0;
    document.body.style.overflowX = "hidden";

    const bgColor1 = initialTheme.COLORMAIN1;
    const bgColor2 = initialTheme.COLORMAIN2;
    const bgColor = `linear-gradient(${bgColor1},${bgColor2})`;

    setLogo(initialTheme.LOGO);
    setBgColor(bgColor);
  }, []);

  function useOutsideAlerter(ref) {
    useEffect(() => {
      /**
       * Alert if clicked on outside of element
       */
      function handleClickOutside(event) {
        if (ref.current && !ref.current.contains(event.target)) {
          setIsShow(false);
        }
      }

      // Bind the event listener
      document.addEventListener("mousedown", handleClickOutside);

      return () => {
        // Unbind the event listener on clean up
        document.removeEventListener("mousedown", handleClickOutside);
      };
    }, [ref]);
  }

  function MenuLeftShow() {
    if (getMenuleft === "show") {
      document.getElementById("MenuLeft").style.left = "0";
      document.getElementById("bgMenuleft").style.display = "block";
      setMenuleft("hide");
    } else {
      document.getElementById("MenuLeft").style.left = "-280px";
      document.getElementById("bgMenuleft").style.display = "none";
      setMenuleft("show");
    }
  }

  const wrapperRef = useRef(null);
  useOutsideAlerter(wrapperRef);

  return (
    <Navbar
      className="x-header"
      bg="light"
      expand="lg"
      style={{
        background: getBgColor,
        // position: activeMenu !== "home" ? "relative" : "none",
      }}
    >
      <Container>
        <Navbar.Brand>
          <a href={THEME === "3" || THEME === "Y" ? "./" : ""}>
            <img
              src={process.env.REACT_APP_IMGURL + "/" + getLogo}
              alt="Logo"
              style={{ maxWidth: "150px", maxHeight: "50px", width: "auto" }}
            />
          </a>
        </Navbar.Brand>
        {/* <Navbar.Toggle aria-controls="basic-navbar-nav" /> */}
        <Navbar.Collapse className="x-menu" id="basic-navbar-nav">
          <Nav className="me-auto -menu-container">
            <Nav.Link
              className={`-menu-link ${activeMenu === "home" ? "active" : ""}`}
              onClick={() => setActiveMenu("home")}
            >
              หน้าแรก
            </Nav.Link>
            <Nav.Link
              className={`-menu-link ${activeMenu === "promotion" ? "active" : ""
                }`}
              onClick={() => setActiveMenu("promotion")}
            >
              โปรโมชั่น
            </Nav.Link>
            <Nav.Link
              className={`-menu-link ${activeMenu === "recommend" ? "active" : ""
                }`}
              onClick={() => setActiveMenu("recommend")}
            >
              รับทรัพย์
            </Nav.Link>
            <Nav.Link
              className={`-menu-link ${activeMenu === "ai" ? "active" : ""}`}
              onClick={() => handleShow("ai")}
            >
              สูตร AI
            </Nav.Link>
            <Nav.Link
              className={`-menu-link ${activeMenu === "activity" ? "active" : ""
                }`}
              onClick={() => redirActivity()}
            >
              กิจกรรม
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>

        {/* For Mobile Devices */}
        <div
          className="-balance-container -balance-container-m d-xl-none"
        >
          <div className="-user-balance js-user-balance f-sm-6 f-7 ">
            <div
              className="-inner-box-wrapper"
              id="recheck_credit"
              style={{ cursor: "pointer" }}
              onClick={(e) => recheckCredit(e)}
            >
              <img
                className="img-fluid -ic-coin"
                src={
                  window.location.origin + "/assets/images/credits-ic-coin.png"
                }
                alt="customer image"
              />
              <span id="customer-balance-m" className="text-green-lighter">
                {credit ? credit : user.lastCredit}
              </span>
              <button
                type="button"
                className="-btn-balance "
                id="btn_refresh_credits"
                data-loading="_onReloadBalance_"
                data-done="_onReloadBalanceDone_"
                data-target="#customer-balance-m"
              >
                <ArrowRepeat />
              </button>
            </div>
            <div className="grar-icon" onClick={() => MenuLeftShow()}>
              <GearFill />
            </div>
          </div>
        </div>
        <MenuLeft
          setActiveMenu={setActiveMenu}
          setMenuleft={setMenuleft}
          initialTheme={initialTheme}
          handleShow={handleShow}
        />
        {/* For Mobile Devices */}

        <Navbar.Collapse id="headerContent">
          <Nav className="d-flex">
            {THEME === "3" || THEME === "Y" ? (
              <a
                href="./"
                className="btn  d-lg-block d-none mr-3 btn-transfer startCheck"
              >
                กระเป๋าเงิน
              </a>
            ) : (
              <>
                <a
                  onClick={() => handleShow("deposit")}
                  className="btn  d-lg-block d-none mr-3 btn-transfer startCheck"
                >
                  ฝากเงิน
                </a>
                <a
                  onClick={() => handleShow("withdraw")}
                  className="btn-transfer btn d-lg-block d-none"
                >
                  ถอนเงิน
                </a>
              </>
            )}

            <div
              className="-balance-container"
            >
              <div className="-user-balance js-user-balance f-sm-6 f-7 ">
                <div
                  className="-inner-box-wrapper"
                  id="recheck_credit"
                  style={{ cursor: "pointer" }}
                  onClick={(e) => recheckCredit(e)}
                >
                  <img
                    className="img-fluid -ic-coin"
                    src={
                      window.location.origin +
                      "/assets/images/credits-ic-coin.png"
                    }
                    alt="customer image"
                  />
                  <span id="customer-balance" className="text-green-lighter">
                    {credit ? credit : user.lastCredit}
                  </span>
                  <button
                    type="button"
                    className="-btn-balance "
                    id="btn_refresh_credits"
                    data-loading="_onReloadBalance_"
                    data-done="_onReloadBalanceDone_"
                    data-target="#customer-balance"
                  >
                    <ArrowRepeat />
                  </button>
                </div>
              </div>
            </div>

            <div className="dropdown menu_profile d-none d-sm-none d-md-block">
              <a
                className="dropdown-toggle"
                id="dropdownMenuButton"
                onClick={handleDropDown}
              >
                <img
                  src={window.location.origin + "/assets/images/ic_profile.png"}
                  className="ic_profile"
                  alt=""
                />
              </a>
              <div
                className={`dropdown-menu ${isShow ? "show" : ""}`}
                aria-labelledby="dropdownMenuButton"
                ref={wrapperRef}
                style={{ background: getBgColor }}
              >
                <a
                  className="dropdown-item"
                  onClick={() => setActiveMenu("profile")}
                >
                  โปรไฟล์
                </a>
                <a
                  className="dropdown-item"
                  onClick={() => setActiveMenu("history")}
                >
                  ประวัติ
                </a>
                <a className="dropdown-item" onClick={() => logOut(HOMEURL)}>
                  ออกจากระบบ
                </a>
              </div>
            </div>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};
export default Header;
