export const MemberList = ({ list, status }) => {
  return (
    <>
      {list.length > 0 ? (
        list.map((item, index) => {
          let depositcomText = "";
          let winlosscomText = "";
          let turnovergamecomText = "";
          if (item.depositcom <= 0) {
            depositcomText = "text-danger";
          }
          if (item.winlosscom <= 0) {
            winlosscomText = "text-danger";
          }
          if (item.turnovergamecom <= 0) {
            turnovergamecomText = "text-danger";
          }

          return (
            <tr key={index}>
              <td className="text-center">{item.memberusername}</td>
              <td className="text-center">{convertPeriod(`${item.period}`)}</td>
              {status.depositstatus === "Y" && (
                <td className={`text-center ${depositcomText}`}>
                  {item.depositcom}
                </td>
              )}
              {status.winlossstatus === "Y" && (
                <td className={`text-center ${winlosscomText}`}>
                  {item.winlosscom}
                </td>
              )}
              {status.turnoverstatus === "Y" && (
                <td className={`text-center ${turnovergamecomText}`}>
                  {item.turnovergamecom}
                </td>
              )}
            </tr>
          );
        })
      ) : (
        <tr>
          <td colSpan="5" className="text-center">
            ไม่พบข้อมูล
          </td>
        </tr>
      )}
    </>
  );
};

export default function Stat({
  dateLogin,
  setAffiliateStatus,
  setCashbackStatus,
}) {
  const user = JSON.parse(localStorage.getItem("user"));
  const [isLoading, setIsLoading] = useState(true);
  const [sumMemberValue, setSumMemberValue] = useState({
    affiliateInfo: {},
    summeriesMember: [],
  });

  const [currentPage, setCurrentPage] = useState(1);
  const [dataPerPage] = useState(10);

  const setUpSumMember = async () => {
    const body = { memberid: user.memberid };
    await axios({
      url: `${appConfig.REACT_APP_APIURL}${getSummariesByMember}`,
      method: "POST",
      timeout: 1000 * 7,
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
      },
      data: body,
    })
      .then((res) => {
        setIsLoading(false);
        let result = res.data.result;
        // result = { ...result, summeriesMember: mockData };
        setSumMemberValue(result);
        setAffiliateStatus(result.affiliateInfo.activestatus);
        setCashbackStatus(result.cashbackInfo[5].configvalue);
      })
      .catch((err) => {
        console.log("error", err);
      });
  };
  useEffect(() => {
    setUpSumMember();
    checkLastLogin();
  }, []);

  const { summeriesMember, affiliateInfo } = sumMemberValue;

  // Get current data
  const indexOfLastPost = currentPage * dataPerPage;
  const indexOfFirstPost = indexOfLastPost - dataPerPage;
  const currentData = summeriesMember.slice(indexOfFirstPost, indexOfLastPost);

  // Change page
  const paginate = (pageNumber) => setCurrentPage(pageNumber);
  return (
    <>
      {!isLoading && (
        <>
          <h3
            className="text-center mb-3 fade-in"
            style={{ fontWeight: "500" }}
          >
            สถิติ
          </h3>
          <div className="row no-gutters">
            <table className="table table-bordered shadow-sm stat">
              <thead>
                <tr>
                  <th className="text-center">ยูสเซอร์เนม</th>
                  <th className="text-center">วันที่เล่น</th>
                  {affiliateInfo.depositstatus === "Y" && (
                    <th className="text-center">ยอดฝาก</th>
                  )}
                  {affiliateInfo.winlossstatus === "Y" && (
                    <th className="text-center">แพ้/ชนะ</th>
                  )}
                  {affiliateInfo.turnoverstatus === "Y" && (
                    <th className="text-center">ค่าคอม</th>
                  )}
                </tr>
              </thead>
              <tbody>
                <MemberList list={currentData} status={affiliateInfo} />
              </tbody>
            </table>
            <Pagination
              dataPerPage={dataPerPage}
              totalData={summeriesMember.length}
              currentPage={currentPage}
              paginate={paginate}
            />
            <div>
              <small>** รายการสรุป การเล่นของสมาชิกที่มีการเล่น</small>
            </div>
          </div>
        </>
      )}
      <Spinner
        animation="border"
        variant="warning"
        style={{
          position: "absolute",
          display: isLoading ? "unset" : "none",
          color: "primary.main",
        }}
      />
    </>
  );
}
