import React, { useEffect, useState } from "react";

import * as Icon from "react-bootstrap-icons";
import Spinner from "react-bootstrap/Spinner";
import Form from "react-bootstrap/Form";

import Modal from "../../../components/Modal";
import { updateStatusPro, checkLastLogin } from "../../../utils/lib";

import moment from "moment";

export default function Profile({
  initialValue,
  setInitialValue,
  initialTheme,
  setIsPage
}) {
  const [isLoading, setIsLoading] = useState(true);
  const [show, setShow] = useState(false);
  const { LINEURL } = initialTheme || "";
  useEffect(() => {
    if (initialValue) {
      setIsLoading(false);
    }
    setIsPage(false);
    checkLastLogin();
  }, [initialValue]);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const {
    bankcode,
    accountnumber,
    bankname,
    accountname,
    memberphone,
    lineid,
    createdate,
    prostatus,
    logindate,
  } = initialValue || "";

  return (
    <div className="container transaction_info mt-4 fade-in">
      <div
        className="row"
        style={{
          opacity: isLoading ? 0.4 : "unset",
          pointerEvents: isLoading ? "none" : "unset",
        }}
      >
        <h4 className="text-center text-highlight mb-3">
          <img
            src={window.location.origin + "/assets/images/ic_profile.png"}
            style={{ display: "inline-block", width: "25px" }}
            alt=""
          />{" "}
          ข้อมูลส่วนตัว
        </h4>
        <div className="col-12">
          <div className="card-bank mb-3">
            <div className="row">
              <div className="col-auto">
                <figure className="avatar avatar-50 active">
                  <img
                    src={
                      window.location.origin +
                      "/assets/images/icon_bank/" +
                      bankcode.toLowerCase() +
                      ".jpg"
                    }
                    title="กสิกร"
                    alt=""
                  />
                </figure>
              </div>
              <div className="col pl-0 align-self-center text-white">
                <p className="mb-0  d-flex">
                  เลขที่บัญชี: {accountnumber ? accountnumber : "-"}
                </p>
                <p className="mb-0 ">ธนาคาร: {bankname ? bankname : "-"}</p>
                <p className="mb-0 text-mute ">
                  ชื่อ: {accountname ? accountname : "-"}
                </p>
              </div>
            </div>
          </div>

          <div className="card-bank">
            <div className="row mb-1 no-gutters">
              <div className="col-5">
                <p className="text-highlight  mb-0">เบอร์โทร:</p>
              </div>
              <div className="col-7">
                <p className="text-white  mb-0">
                  {memberphone ? memberphone : "-"}
                </p>
              </div>
            </div>
            <div className="row mb-1 no-gutters">
              <div className="col-5">
                <p className="text-highlight  mb-0">รหัสผ่าน:</p>
              </div>
              <div className="col-6">
                <p className="text-white  mb-0">xxxxx</p>
              </div>
              <div className="col-1">
                <span className="text-warning">
                  <i className="fa fa-pen fa-sm"></i>
                </span>
              </div>
            </div>
            <div className="row mb-1 no-gutters">
              <div className="col-5">
                <p className="text-highlight  mb-0">ไอดีไลน์:</p>
              </div>
              <div className="col-7">
                <p className="text-white  mb-0">{lineid ? lineid : "-"}</p>
              </div>
            </div>

            <div className="row mb-1 no-gutters">
              <div className="col-5">
                <p className="text-highlight  mb-0">สมัครสมาชิกเมื่อ:</p>
              </div>
              <div className="col-7">
                <p className="text-white  mb-0">
                  {createdate
                    ? moment(createdate).format("DD/MM/YYYY HH:mm") + " น."
                    : "-"}
                </p>
              </div>
            </div>

            <div className="row mb-1 no-gutters">
              <div className="col-5">
                <p className="text-highlight  mb-0">
                  {" "}
                  เข้าสู่ระบบล่าสุดเมื่อ:{" "}
                </p>
              </div>
              <div className="col-7">
                <p className="text-white  mb-0">
                  {logindate
                    ? moment(logindate).format("DD/MM/YYYY HH:mm") + " น."
                    : "-"}
                </p>
              </div>
            </div>

            <div
              className="mt-1 mb-1 no-gutters"
              style={{ display: "block", textAlign: "center" }}
            >
              <label for="f-option" className="l-radio">
                <Form.Check
                  inline
                  label="รับโบนัส"
                  name="prostatus"
                  type="radio"
                  id="f-option-1"
                  onChange={() =>
                    updateStatusPro("Y", setIsLoading, setInitialValue)
                  }
                  checked={prostatus === "Y"}
                />
              </label>
              <label for="s-option" className="l-radio">
                <Form.Check
                  inline
                  label="ไม่รับโบนัส"
                  name="prostatus"
                  type="radio"
                  id="f-option-2"
                  onChange={() =>
                    updateStatusPro("N", setIsLoading, setInitialValue)
                  }
                  checked={prostatus === "N"}
                />
              </label>
            </div>

            <div className="row mb-1 no-gutters">
              <div className="col-12">
                <button
                  type="bytton"
                  onClick={handleShow}
                  className="btn-none google btn mybtn btn-change-password"
                >
                  <Icon.PencilSquare /> เปลี่ยนรหัสผ่าน
                </button>
              </div>
            </div>
          </div>

          <div className="bank_info mt-4 ">
            <h4
              className="text-left text-highlight mb-3"
              style={{ textAlign: "left" }}
            >
              หมายเหตุ
            </h4>
            <p className="text-left text-highlight mb-1">
              - หากต้องการแก้ไขข้อมูลส่วนตัวกรุณาติดต่อเจ้าหน้าที่
            </p>
            <p className="text-center mt-4">
              <a
                href={LINEURL ? LINEURL : ""}
                style={{ color: "#000", textDecoration: "none" }}
                className="btn-main btn-block mybtn btn_bg icon tx-tfm"
              >
                <Icon.PeopleFill /> ติดต่อเจ้าหน้าที่
              </a>
            </p>
          </div>
        </div>
      </div>
      <Modal
        show={show}
        setShow={setShow}
        handleClose={handleClose}
        type={"changePassword"}
      />
      <Spinner
        animation="border"
        variant="warning"
        style={{
          position: "absolute",
          display: isLoading ? "unset" : "none",
          color: "primary.main",
        }}
      />
    </div>
  );
}
