/* eslint-disable no-script-url */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState, useEffect } from "react";
import { categoryList } from "./DefaultValue";

import { gameListM } from "../../../utils/apiPath";
import {
  chkMobile,
  appConfig,
  checkLastLogin,
  checkLastLoginReturnVal,
  logOut,
  initialTheme,
} from "../../../utils/lib";

import { v4 as uuidv4 } from "uuid";
import Swal from "sweetalert2";
import axios from "axios";
// console.log('appConfig',appConfig);
export const GameList = ({
  category,
  list,
  setActiveMenu,
  setSubgameId,
  device,
  setIsLoading,
  setMaingamename,
  dateLogin,
}) => {
  const filterList =
    category !== "ALL"
      ? list.length > 0
        ? list.filter((item) => item.gametype === category)
        : ""
      : list;

  const setsubgame = (id, gamename) => {
    setActiveMenu("subgame");
    setMaingamename(gamename);
    setSubgameId(id);
  };

  const reGame = async (link) => {
    // checkLastLogin();
    setIsLoading(false);
    var stustLogin = await checkLastLoginReturnVal(dateLogin);
    if (stustLogin) {
      await axios({
        url: `${appConfig.REACT_APP_APIURL}${link}${device}`,
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
        },
      }).then((res) => {
        setIsLoading(true);
        if (res.data.datas.code === 0) {
          const urlgame = res.data.datas.url;
          setTimeout(() => {
            window.open(urlgame, "_blank");
          });
        } else {
          Swal.fire({
            text: res.data.datas.message,
            icon: "error",
            confirmButtonText: "Close",
          });
        }
      });
    } else {
      logOut();
    }
  };

  return (
    <>
      {filterList.length > 0 &&
        filterList.map((item) => {
          const linker = item.link.split("/");
          // console.log(linker[1])
          return (
            <>
              {item.gameid !== 5 && (
                <li key={uuidv4()} className="nav-item">
                  {linker[1] === "brandgame" ? (
                    <div
                      className="gametype pointer"
                      data-member_game_id={item.member_game_id}
                      data-gameid={item.gameid}
                      data-gametype={item.gametype}
                      rel="noreferrer"
                      onClick={() => {
                        setsubgame(item.gameid, item.gamename);
                      }}
                    >
                      <div
                        className="x-game-list-item-macro js-game-list-toggle -big "
                        data-status=""
                      >
                        <div className="-inner-wrapper ">
                          <img
                            src={`${
                              window.location.origin
                            }/assets/images/${item.gamecode.toLowerCase()}.png?${appConfig.REACT_APP_IMGVER}`}
                            className="-cover-img lazyload img-game img-fluid"
                            alt={item.gamename}
                          />
                        </div>
                      </div>
                    </div>
                  ) : (
                    <button
                      type="button"
                      className="gametype btn-none p-0"
                      data-member_game_id={item.member_game_id}
                      data-gameid={item.gameid}
                      data-gametype={item.gametype}
                      rel="noreferrer"
                      onClick={() => reGame(item.link)}
                    >
                      <div
                        className="x-game-list-item-macro js-game-list-toggle -big "
                        data-status=""
                      >
                        <div className="-inner-wrapper ">
                          <img
                            src={`${
                              window.location.origin
                            }/assets/images/${item.gamecode.toLowerCase()}.png?${appConfig.REACT_APP_IMGVER}`}
                            className="-cover-img lazyload img-game img-fluid"
                            alt={item.gamename}
                          />
                        </div>
                      </div>
                    </button>
                  )}
                </li>
              )}
            </>
          );
        })}
    </>
  );
};

export const Home = ({
  dateLogin,
  setSubgameId,
  setActiveMenu,
  setIsLoading,
  setMaingamename,
}) => {
  const [gameCategoryActive, setGameCategoryActive] = useState("ALL");
  const [getBgColor, setBgColor] = useState("");
  const [device, setDevice] = useState("");
  const [initialGame, setInitialGame] = useState({
    gameid: "",
    gametype: "",
    openexternal: "",
  });

  useEffect(() => {
    const bgColor1 = initialTheme.COLORMAIN1;
    const bgColor2 = initialTheme.COLORMAIN2;
    const bgColor = `linear-gradient(${bgColor1},${bgColor2})`;

    setDevice(chkMobile);
    setBgColor(bgColor);
    gameListMain();
    checkLastLogin();
  }, []);

  const gameListMain = async () => {
    // setIsLoading(false);
    const memberid = JSON.parse(localStorage.getItem("user"));
    await axios({
      url: `${appConfig.REACT_APP_APIURL}${gameListM}`,
      method: "POST",
      data: { memberid: memberid.memberid },
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
      },
    }).then((res) => {
      const result = res.data.result.rows;
      setInitialGame(result);
      // setIsLoading(true);
    });
  };

  return (
    <div className="container home_page">
      <section className="row x-category-index">
        <div
          className="-nav-menu-container col-xl-3 col-lg-3 js-category-menus"
          style={{ background: getBgColor }}
        >
          <nav className="nav-menu">
            <ul
              className="-menu-parent navbar-nav js-menu-container nav nav-tabs"
              role="tablist"
              id="accordion-games"
            >
              {categoryList &&
                categoryList.map((item, index) => {
                  return (
                    <li
                      className={`-list-parent nav-item ${item.iconClass} `}
                      key={index}
                    >
                      
                      <a
                        id="allgame-tab"
                        className={`-menu-btn -parent nav-link  ${
                          gameCategoryActive === item.type ? "active" : ""
                        }`}
                        data-toggle="tab"
                        role="tab"
                        aria-controls="allgame"
                        aria-selected="true"
                        onClick={() => {
                          setGameCategoryActive(item.type);
                        }}
                      >
                        <img
                        className="-img-category"
                        src={
                          window.location.origin +
                          "/assets/images/" +
                          item.image
                        }
                      />
                        {/* <div className="-img-category"></div> */}
                        <div className="-menu-text">
                          <span className="-menu-text-main">{item.name}</span>
                        </div>
                      </a>
                    </li>
                  );
                })}
            </ul>
          </nav>
        </div>
        <div className="-games-list-container js-game-scroll-container js-game-container col">
          <div className="-games-list-wrapper">
            <div className="tab-content">
              <div
                className="tab-pane active"
                id="allgame"
                role="tabpanel"
                aria-labelledby="allgame-tab"
              >
                <ul className="navbar-nav">
                  <GameList
                    category={gameCategoryActive}
                    list={initialGame}
                    setActiveMenu={setActiveMenu}
                    setSubgameId={setSubgameId}
                    device={device}
                    setIsLoading={setIsLoading}
                    setMaingamename={setMaingamename}
                    dateLogin={dateLogin}
                  />
                </ul>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Home;
