import React, { useState, useEffect } from "react";
import { memberHistory } from "../../../utils/apiPath";
import { appConfig, checkLastLogin } from "../../../utils/lib";

import axios from "axios";
import Spinner from "react-bootstrap/Spinner";

export default function History({ setIsPage }) {
  const [history, setHistory] = useState("");
  const [isLoading, setIsLoading] = useState(true);

  const HistoryList = async () => {
    const memberid = JSON.parse(localStorage.getItem("user"));

    await axios({
      url: `${appConfig.REACT_APP_APIURL}${memberHistory}`,
      method: "POST",
      data: { memberid: memberid.memberid },
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${appConfig.REACT_APP_KEYBRAVO}`,
      },
    }).then((res) => {
      const result = res.data.result;
      setHistory(result);
      setIsLoading(false);
      // console.log(result);
    });
  };

  useEffect(() => {
    HistoryList();
    checkLastLogin();
    setIsPage(false);
  }, []);

  const Historylistshow = ({ list }) => {
    return (
      <>
        <tbody>
          {list.length > 0 ? (
            list.map((item, index) => {
              return (
                <tr key={index}>
                  <td className="text-center">
                    <b>{item.transferinfodate}</b>
                  </td>
                  <td className="text-center">
                    {item.transfertype === "D" ? (
                      <b className="text-success">ฝาก</b>
                    ) : (
                      <b className="text-danger">ถอน</b>
                    )}
                  </td>
                  <td className="text-center">
                    {item.transfertype === "D" ? (
                      <b className="text-success">{item.transferamount}</b>
                    ) : (
                      <b className="text-danger">{item.transferamount}</b>
                    )}
                  </td>
                  <td className="text-center">
                    {item.bonustype === null ? (
                      <b>0.00</b>
                    ) : (
                      <b>{item.bonustype}</b>
                    )}
                  </td>
                </tr>
              );
            })
          ) : (
            <tr>
              <td colSpan="4" className="text-center">
                {isLoading ? "กำลังโหลดข้อมูล..." : "ไม่พบข้อมูล"}
              </td>
            </tr>
          )}
        </tbody>
      </>
    );
  };

  return (
    <div className="container transaction_info mt-4 fade-in">
      <div
        className="row"
        style={{
          opacity: isLoading ? 0.4 : "unset",
          pointerEvents: isLoading ? "none" : "unset",
        }}
      >
        <h4 className="text-center text-highlight mb-3">
          <img
            src={window.location.origin + "/assets/images/ic_history.png"}
            style={{ display: "inline-block", width: "25px" }}
            alt=""
          />{" "}
          ประวัติธุรกรรม
        </h4>
        <div className="col-12 col-sm-12 col-md-12 mt-2">
          <table className="table table-bordered shadow-sm">
            <thead>
              <tr>
                <th scope="col" className="text-center">
                  วันเวลา
                </th>
                <th scope="col" className="text-center">
                  ประเภท
                </th>
                <th scope="col" className="text-center">
                  จำนวน
                </th>
                <th scope="col" className="text-center">
                  โปรโมชั่น
                </th>
              </tr>
            </thead>
            <Historylistshow list={history} />
          </table>
        </div>
      </div>
      <Spinner
        animation="border"
        variant="warning"
        style={{
          position: "absolute",
          display: isLoading ? "unset" : "none",
          color: "primary.main",
        }}
      />
    </div>
  );
}
