import React, { useState, useEffect } from "react";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
} from "react-router-dom";

import preval from "preval.macro";
import moment from "moment";

// import { Helmet } from "react-helmet";
// import Head from "next/head";
import MetaTags from "react-meta-tags";

import Login from "./modules/Login/Login";
import Register from "./modules/Register/Register";
import WelletLayout from "./modules/Wallet/Layout/Layout";
import GameListLayout from "./modules/GameList/Layout/Layout";
import {
  checkLoginExpire,
  setUpLastLogin,
  initialTheme,
  logOut,
} from "./utils/lib";
import "./utils/MainStyle.css";

function App() {
  const dateLogin = localStorage.getItem("dateLogin")
    ? localStorage.getItem("dateLogin")
    : "";
  const token = localStorage.getItem("accessToken");
  const dateBuildTimeStamp = preval`module.exports = new Date();`;
  const dateBuild = moment(dateBuildTimeStamp).format("DD/MM/YYYY HH:mm:ss");

  const { SYSTEMNAME, THEME, LOGO } = initialTheme || "";

  const title = SYSTEMNAME ? `BIGWIN - ${SYSTEMNAME}` : `BIGWIN`;

  let _THEME_ = parseInt(THEME) === 2 ? 2 : 3;

  if (initialTheme) {
    const bgColor2 = initialTheme.COLORBG;
    document.body.style.background = bgColor2;
  }

  // useEffect(() => {
  //   if (token) {
  //     setUpLastLogin();
  //   }
  // }, []);

  if (dateLogin) {
    checkLoginExpire(dateLogin);
    // check version build //
    const dateLoginFormat = moment(dateLogin).format("DD/MM/YYYY HH:mm:ss");
    // console.log('time',dateLoginFormat);

    if (dateLoginFormat < dateBuild) {
      logOut();
    }
  }

  (function () {
    window.onpageshow = function (event) {
      if (event.persisted) {
        window.location.reload();
      }
    };
  })();

  if (!token) {
    localStorage.removeItem("accessToken");
    localStorage.removeItem("dateLogin");
    localStorage.removeItem("user");
    localStorage.removeItem("tour");
    return (
      <Router>
        {initialTheme && (
          <MetaTags>
            <title>{title}</title>
            <meta name="description" content={title} />
          </MetaTags>
        )}
        <Routes>
          <Route path="/login" element={<Login />} />
          <Route path="/register" element={<Register />} />
          <Route path="*" element={<Navigate to="/login" />} />
          {/* <Route path="/:prefix/login" element={<Login />} />*/}
        </Routes>
      </Router>
    );
  } else if (token && parseInt(_THEME_) === 1) {
    return (
      <Router>
        {initialTheme && (
          <MetaTags>
            <title>{title}</title>
            <meta name="description" content={title} />
          </MetaTags>
        )}
        <Routes>
          <Route path="/" element={<WelletLayout />}></Route>
          <Route path="/Gamelist" element={<WelletLayout />}></Route>
          <Route path="*" element={<Navigate to="/" />} />
        </Routes>
      </Router>
    );
  } else if (token && parseInt(_THEME_) === 2) {
    return (
      <Router>
        {initialTheme && (
          <MetaTags>
            <title>{title}</title>
            <meta name="description" content={title} />
          </MetaTags>
        )}
        <Routes>
          <Route path="/" element={<GameListLayout />}></Route>
          <Route path="/Gamelist" element={<GameListLayout />}></Route>
          <Route path="*" element={<Navigate to="/" />} />
        </Routes>
      </Router>
    );
  } else if (token && parseInt(_THEME_) === 3) {
    return (
      <Router>
        {initialTheme && (
          <MetaTags>
            <title>{title}</title>
            <meta name="description" content={title} />
          </MetaTags>
        )}
        <Routes>
          <Route path="/" element={<WelletLayout />}></Route>
          <Route path="/Gamelist" element={<GameListLayout />}></Route>
          <Route path="*" element={<Navigate to="/" />} />
        </Routes>
      </Router>
    );
  }

  // return (
  //   <div>
  //     <Router>
  //       {!token ? (
  //         <Routes>
  //           <Route path="/login" element={<Login />}></Route>
  //           <Route path="/register" element={<Register />}></Route>
  //           <Route path="/Gamelist" element={<Login />}></Route>
  //           <Route path="/" element={<Login />}></Route>
  //         </Routes>
  //       ) : (
  //         <Routes>
  //           <Route path="/" element={<WelletLayout />}></Route>
  //           <Route path="/Gamelist" element={<GameListLayout />}></Route>
  //           <Route path="*" element={<NotFound />} />
  //         </Routes>
  //       )}
  //     </Router>
  //   </div>
  // );
}

export default App;
